<template>
  <div class="body">
    <div class="container">
      <div class="system-content">
        <el-tabs v-model="currentTab">
          <!--  -->
          <el-tab-pane :label="name+''" name="0">
            <v-search :search-text="name+''" style="margin-bottom: 20px;" @search="handleSearch"></v-search>

            <v-table :data="table" :column="column" :action="[]"></v-table>
            
            <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane :label="name+''" name="1">
            <el-form ref="createRef" label-width="100px" label-position="left" style="width: 50%;margin: 0 auto;" :model="createModel">
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="name">
                <el-input v-model.trim="createModel.name"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
                <el-input v-model.trim="createModel.description"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="version">
                <el-input v-model.trim.number="createModel.version"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="protocol">
                <el-input v-model.trim="createModel.protocol"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="address">
                <el-input v-model.trim="createModel.address"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="authorize">
                <el-input v-model.trim.number="createModel.authorize"></el-input>
              </el-form-item>
              <el-form-item label="token" :rules='[{required: true, message: "token"}]' prop="token">
                <el-input v-model.trim="createModel.token"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="username">
                <el-input v-model.trim="createModel.username"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="password">
                <el-input v-model.trim="createModel.password"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="perms">
                <el-input v-model.trim="createModel.perms"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' class="btn" @click="handleCreate"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane :label="name+''" name="2" v-if="this.updateId">
            <el-form ref="updateRef" label-width="100px" label-position="left" style="width: 50%;margin: 0 auto;" :model="updateModel">
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="name">
                <el-input v-model.trim="updateModel.name"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
                <el-input v-model.trim="updateModel.description"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="version">
                <el-input v-model.trim.number="updateModel.version"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="protocol">
                <el-input v-model.trim="updateModel.protocol"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="address">
                <el-input v-model.trim="updateModel.address"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="authorize">
                <el-input v-model.trim.number="updateModel.authorize"></el-input>
              </el-form-item>
              <el-form-item label="token" :rules='[{required: true, message: "token"}]' prop="token">
                <el-input v-model.trim="updateModel.token"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="username">
                <el-input v-model.trim="updateModel.username"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="password">
                <el-input v-model.trim="updateModel.password"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="perms">
                <el-input v-model.trim="updateModel.perms"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' class="btn" @click="handleUpdate"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
  import {page, service, oplog} from '../../api/index.js'
  export default {
    data () {
      return {
        name: '',
        column: service.column,
        table: [],
        total: 0,
        search: {
          q: '',
          pageSize: 10,
          pageNumber: 1
        },
        currentTab: '0',
        updateId: '',
        createModel: {
          name: '',
          description: '',
          version: '',
          protocol: '',
          address: '',
          authorize: '',
          token: '',
          username: '',
          password: '',
          perms: ''
        },
        updateModel: {
          name: '',
          description: '',
          version: '',
          protocol: '',
          address: '',
          authorize: '',
          token: '',
          username: '',
          password: '',
          perms: ''
        }
      }
    },
    mounted () {
      this.handleSearch()
    },
    methods: {
      handleCreate () {
        this.$refs.createRef.validate(valid => {
          if (valid) {
            service.create(this.createModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.createRef.resetFields()
                this.handleSearch()
                this.$message({type: 'success', message: ''+this.name+'', duration: 1000})
                oplog.create(`${this.name}`)
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleUpdate () {
        this.$refs.updateRef.validate(valid => {
          if (valid) {
            page.put(this.updateId, this.updateModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.updateRef.resetFields()
                this.$message({type: 'success', message: ''+this.name+'', duration: 1000})
                this.updateId = ''
                this.currentTab = '0'
                this.handleSearch()
                oplog.create(`${this.name}`)
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleSearch (model) {
        if (model !== undefined) {
          this.search.q = model
        }
        service.search(this.search.q, this.search.pageNumber, this.search.pageSize).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.table = res.data.data
            this.total = parseInt(res.data.total)
            oplog.create(`${this.name}`)
          }
        })
      },
      handleAction (type, row) {
        if (type == 'delete') {
          page.delete(row.id).then(res => {
            if (res.data && res.data.status.code == '0') {
              // let index = this.table.indexOf(row)
              this.handleSearch()
              oplog.create(`${this.name}`)
            }
          })
        } else {
          this.currentTab = '2'
          this.updateId = row.id
          for (var i in this.updateModel) {
            this.updateModel[i] = row[i]
          }
        }
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleSearch()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleSearch()
      }
    }
  }
</script>


