<template>
  <div class="system-content">
    <div class="face">
    <v-search btn-text="" search-text="" @btn="dialog.create.show = true" @search="handleSearch"></v-search>
    </div>
  </div>
</template>

<script>
  import {face} from '../../api/index.js'
  export default {
    data () {
      return {
        table: []
      }
    },
    mounted () {
      this.handleSearch()
    },
    methods: {
      handleSearch () {
        face.search(this.$route.params.id, '', 1, 10)
      }
    }
  }
</script>


