<template>
  <div class="system-content">
    <div style="background: #fff;padding: 12px 10px 0 20px;">
      <el-row :gutter="50" v-show="treeShow">
        <el-col :span="8">
          <div style="">
            <el-input style="margin-bottom: 12px;" v-model="filterText"></el-input>
            <el-tree style="margin-bottom: 12px;"
              :data="treeCate" 
              :props="props" 
              ref="tree2"
              :show-checkbox="true" 
              :highlight-current="false"
              :expand-on-click-node="true"
              :filter-node-method="filterNode"
              @node-click="handleNodeClick"
              @check-change="handleCheckChange">
            </el-tree>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="50">
        <el-col :span="8">
          <el-input placeholder="" @focus="handleInputFocus"></el-input>
        </el-col>
        <el-col :span="8">
          <el-select style="width: 100%;" placeholder="" v-model="search.hasAlarm">
            <el-option label="" :value="-1"></el-option>
            <el-option v-for="item in selectAlarm" :key="item.label" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-col>
        <el-col :span="8">
          <el-select style="width: 100%;" placeholder="" v-model="search.compareType">
            <el-option label="" :value="-1"></el-option>
            <el-option v-for="item in selectFrom" :key="item.label" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-col>
      </el-row>
    </div>
    <v-search search-text="" @search="handleSearch">
      <div style="width: 50%;" slot="left">
        <el-date-picker type="date" placeholder="" v-model="search.started"></el-date-picker>
        <span>-</span>
        <el-date-picker type="date" placeholder="" v-model="search.ended"></el-date-picker>
      </div>
    </v-search>
  
    <div class="flex">
      <div class="flex-item" style="position: relative;">
        <v-table :data="table" :column="column" :action="[]">
          <el-table-column label="" prop="capturedFaceImageUrl" align="center" width="140px" slot="capturedFaceImageUrl">
            <template scope="scope">
              <div class="table-img">
                <img :src="scope.row.capturedFaceImageUrl">
              </div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="faceImageUrl" align="center" width="140px" slot="faceImageUrl">
            <template scope="scope">
              <div class="table-img">
                <img :src="scope.row.faceImageUrl">
              </div>
            </template>
          </el-table-column>
        </v-table>

      <!-- <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page> -->
      </div>
    </div>
  </div>
</template>

<script>
  import {om, log} from '../../api/index.js'
  export default {
    data () {
      return {
        filterText: '',
        props: {
          label: 'name',
          children: 'children'
        },
        column: [
          {label: '', prop: 'capturedFaceImageUrl', type: 'slot'},
          {label: '', prop: 'faceImageUrl', type: 'slot'},
          {label: '', prop: 'hasAlarm'},
          // {label: '', prop: 'type'},
          // {label: '', prop: 'fc_name'},
          {label: '', prop: 'idcardName'},
          // {label: '', prop: 'device'},
          {label: '', prop: 'created'}
        ],
        table: [],
        total: 0,
        treeShow: false,
        treeCate: [],
        treeD: [],
        checked: [],
        search: {
          q: '',
          pageNumber: 1,
          pageSize: 10,
          deviceId: [],
          started: '',
          ended: '',
          compareType: '',
          hasAlarm: ''
        },
        selectAlarm: [
          {label: '', value: 1},
          {label: '', value: 0}
        ],
        selectFrom: [
          {label: '', value: 0},
          {label: '1:N', value: 1},
          {label: '', value: 2},
          {label: '', value: 3}
        ]
      }
    },
    watch: {
      filterText(val) {
        this.$refs.tree2.filter(val)
      }
    },
    methods: {
      handleSearch () {
        let pa = {}
        for (var i in this.search) {
          if (i == 'deviceId') {
            if (this.search[i].length > 0) {
              pa[i] = this.search[i]
            }
          } else if (i == 'started' && this.search[i] !== '' || i == 'ended' && this.search[i] !== '') {
            console.log(this.$moment(this.search[i]).format('YYYY-MM-DD'))
            pa[i] = Date.parse(new Date(this.search[i]))
          } else {
            if (this.search[i] !== '' && this.search[i] !== -1) {
              pa[i] = this.search[i]
            }
          }
          
        }
        log.search(pa).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.table = res.data.data

          }
        })
      },
      handleInputFocus () {
        this.treeShow = true
        om.search('', 1, 10).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.treeCate = this.jsonTree(res.data.data, 'id', 'parentId', 'children')
          }
        })
      },
      handleNodeClick (a, b, c) {
        if (!this.showCheckbox) {
          this.checked.push(a)
        }
      },
      handleCheckChange (data, checked, indeterminate) {
        if (checked) {
          this.checked.push(data)
        } else {
          let i = this.checked.indexOf(data)
          this.checked.splice(i, 1)
        }
      },
      filterNode(value, data) {
        console.log(value)
        if (!value) return true;
        return data.name.indexOf(value) !== -1;
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleSearch()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleSearch()
      },
      jsonTree (json, id, pid, children) {
        var r = [], hash = {}, i = 0, j = 0, len = json.length
        for (;i < len; i++) {
          hash[json[i][id]] = json[i]
        }
        for (;j < len; j++) {
          var val = json[j], hval = hash[val[pid]]
          if (hval) {
            !hval[children]&&(hval[children] = [])
            hval[children].push(val)
          } else {
            r.push(val)
          }
        }
        return r
      }
    }
  }
</script>


