<template>
  <div class="body">
    <div class="container">
      <search-tree @search="handleBtnSearch" :tree-data="tree" @update="handleTreeUpdate"></search-tree>

      <div class="repo-list">
        <repo-item v-for="(item, i) in table" :key="i" :data="item"></repo-item>
      </div>

      <v-page ></v-page>
    </div>
  </div>
</template>

<script>
  import repoItem from '../../shared/components/repo-item.vue'
  import {om, alarm, log, device} from '../../api/index.js'
  export default {
    components: {
      repoItem
    },
    data () {
      return {
        tree: [],
        table: [],
        logs: [],
        alarms: [],
        unitIds: [],
        deviceIds: []
      }
    },
    methods: {
      handleSearch () {

      },
      handleBtnSearch (q) {
        om.search(q, 1, 10).then(res => {
          this.tree = res.data.data
        })
      },
      handleTreeUpdate (data) {
        this.unitIds = []
        this.deviceIds = []
        data.map(item => {
          this.unitIds.push(item.id)
        })
        device.search({unitIds: this.unitIds, pageNumber: 1, pageSize: 6}).then(dev => {
          if (dev.data && dev.data.status.code == 0) {
            this.table = dev.data.data
            this.table.map(item => {
              this.deviceIds.push(item.id)
            })
            alarm.search({deviceIds: this.deviceIds, pageNumber: 1, pageSize: 6}).then(ala => {
              if (ala.data && ala.data.status.code == 0) {
                this.table.map(item => {
                  ala.data.data.map(key => {
                    if (item.id == key.deviceId) {
                      if (item.deviceAlarm == undefined) {
                        this.$set(item, 'deviceAlarm', key)
                      }
                    }
                  })
                })
              }
            })
            log.search({deviceIds: this.deviceIds, pageNumber: 1, pageSize: 6}).then(p => {
              if (p.data && p.data.status.code == 0) {
                this.table.map(item => {
                  p.data.data.map(key => {
                    if (item.id == key.deviceId) {
                      if (item.deviceLog == undefined) {
                        this.$set(item, 'deviceLog', key)
                      }
                    }
                  })
                })
              }
            })
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .repo-list 
    font-size 0
    margin-left -10px
    margin-right -10px
    padding-bottom 20px
</style>


