<template>
  <div class="body">
    <div class="container">
      <div class="system-content">
        <el-tabs v-model="currentTab" @tab-click="handleTabClick">
          <!--  -->
          <el-tab-pane :label="name+''" name="0">
            <v-search :search-text="name+''" style="margin-bottom: 20px;" @search="handleSearch"></v-search>

            <v-table :data="table" :column="column" @update="handleAction"></v-table>
            
            <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane :label="name+''" name="1">
            <el-form ref="createRef" label-width="100px" label-position="left" style="width: 50%;margin: 0 auto;" :model="createModel">
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="name">
                <el-input v-model.trim="createModel.name"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="parentId">
                <el-select v-model.trim="createModel.parentId" style="width: 100%;">
                  <el-option label="" value="0"></el-option>
                  <el-option v-for="item in parentIds" :key="item.id" :label="item.name" :value="item.id"> 
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="routeUrl">
                <el-input v-model.trim="createModel.routeUrl"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="routePrefix">
                <el-input v-model.trim="createModel.routePrefix"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="sequence">
                <el-input v-model.trim.number="createModel.sequence"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
                <el-input v-model.trim="createModel.description"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="uiTemplate">
                <el-select v-model.trim="createModel.uiTemplate" style="width: 100%;">
                  <el-option v-for="item in templates" :key="item.label" :label="item.label" :value="item.value">
                    <span style="float: left;">{{item.label}}</span>
                    <span style="float: right; color: #8492a6;">{{item.value}}</span>
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="ID" :rules='[{required: true, message: "ID"}]' prop="serviceId">
                <el-select v-model.trim="createModel.serviceId" style="width: 100%;">
                  <el-option v-for="item in services" :key="item.id" :label="item.name" :value="item.id">
                    <span style="float: left;">{{item.name}}</span>
                    <span style="float: right; color: #8492a6;">{{item.id}}</span>
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' class="btn" @click="handleCreate"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane :label="name+''" name="2" v-if="this.updateId">
            <el-form ref="updateRef" label-width="100px" label-position="left" style="width: 50%;margin: 0 auto;" :model="updateModel">
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="name">
                <el-input v-model.trim="updateModel.name"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="parentId">
                <el-select v-model.trim="updateModel.parentId" style="width: 100%;">
                  <el-option label="" value="0"></el-option>
                  <el-option v-for="item in parentIds" :key="item.id" :label="item.name" :value="item.id"> 
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="routeUrl">
                <el-input v-model.trim="updateModel.routeUrl"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="routePrefix">
                <el-input v-model.trim="updateModel.routePrefix"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="sequence">
                <el-input v-model.trim.number="updateModel.sequence"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
                <el-input v-model.trim="updateModel.description"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="uiTemplate">
                <el-select v-model.trim="updateModel.uiTemplate" style="width: 100%;">
                  <el-option v-for="item in templates" :key="item.label" :label="item.label" :value="item.value">
                    <span style="float: left;">{{item.label}}</span>
                    <span style="float: right; color: #8492a6;">{{item.value}}</span>
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="ID" :rules='[{required: true, message: "ID"}]' prop="serviceId">
                <el-select v-model.trim="updateModel.serviceId" style="width: 100%;">
                  <el-option v-for="item in services" :key="item.id" :label="item.name" :value="item.id">
                    <span style="float: left;">{{item.name}}</span>
                    <span style="float: right; color: #8492a6;">{{item.id}}</span>
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' class="btn" @click="handleUpdate"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
  
</template>

<script>
  import {page, oplog, service} from '../../api/index.js'
  export default {
    data () {
      return {
        name: '',
        column: page.column,
        table: [],
        parentIds: [],
        total: 0,
        search: {
          q: '',
          pageSize: 10,
          pageNumber: 1
        },
        currentTab: '0',
        updateId: '',
        createModel: {
          name: '',
          parentId: '0',
          routeUrl: '',
          routePrefix: '',
          sequence: '',
          description: '',
          uiTemplate: '',
          serviceId: ''
        },
        updateModel: {
          name: '',
          parentId: '',
          routeUrl: '',
          routePrefix: '',
          sequence: '',
          description: '',
          uiTemplate: '',
          serviceId: ''
        },
        services: [],
        templates: [
          {label: '', value: 'layout'},
          {label: '', value: 'home'},
          {label: '-', value: 'homeLight'},
          {label: '', value: 'repo'},
          {label: '', value: 'checkFace'},
          {label: '', value: 'checkIdcard'},
          {label: '', value: 'checkCompare'},
          {label: '', value: 'resourceControlCog'},
          {label: '', value: 'resourceDevice'},
          {label: '', value: 'resourceControl'},
          {label: '', value: 'resourceFaceset'},
          {label: '', value: 'omUnitCategory'},
          {label: '', value: 'omUnitManage'},
          {label: '', value: 'userGroupManage'},
          {label: '', value: 'userManage'},
          {label: '', value: 'userGroupPerm'},
          {label: '', value: 'userPerm'},
          {label: '', value: 'serviceManage'},
          {label: '', value: 'pageManage'},
          {label: '', value: 'logManage'},
        ]
      }
    },
    mounted () {
      this.handleSearch()
      this.handleService()
      this.handleParentIds()
    },
    methods: {
      handleCreate () {
        this.$refs.createRef.validate(valid => {
          if (valid) {
            page.create(this.createModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.createRef.resetFields()
                this.handleSearch()
                this.$message({type: 'success', message: ''+this.name+'', duration: 1000})
                oplog.create(`${this.name}`)
                this.handleRouterUpdate()
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleUpdate () {
        this.$refs.updateRef.validate(valid => {
          if (valid) {
            page.put(this.updateId, this.updateModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.updateRef.resetFields()
                this.$message({type: 'success', message: ''+this.name+'', duration: 1000})
                this.updateId = ''
                this.currentTab = '0'
                this.handleSearch()
                oplog.create(`${this.name}`)
                this.handleRouterUpdate()
                this.$router.go(0)
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleSearch (model) {
        if (model !== undefined) {
          this.search.q = model
        }
        page.search(this.search.q, this.search.pageNumber, this.search.pageSize).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.table = res.data.data
            this.total = parseInt(res.data.total)
            oplog.create(`${this.name}`)
          }
        })
      },
      handleParentIds () {
        this.$store.state.backRouters.map(item => {
          if (item.parentId == 0) {
            this.parentIds.splice(item.sequence, 1, item)
          }
        })
      },
      handleService () {
        service.search('', 1, 100).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.services = res.data.data
            this.createModel.serviceId = this.services[0].id
          }
        })
      },
      handleTabClick (key) {
        if (key.name !== 2) {
          this.updateId = ''
        }
      },
      handleRouterUpdate () {
        page.router().then(res => {
          if (res.data && res.data.status.code == 0) {
            this.$store.dispatch('handleRouters', res.data.data)
          }
        })
      },
      handleAction (type, row) {
        if (type == 'delete') {
          page.delete(row.id).then(res => {
            if (res.data && res.data.status.code == '0') {
              // let index = this.table.indexOf(row)
              this.handleSearch()
              oplog.create(`${this.name}`)
              this.handleRouterUpdate()
            }
          })
        } else {
          this.currentTab = '2'
          this.updateId = row.id
          for (var i in this.updateModel) {
            this.updateModel[i] = row[i]
          }
        }
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleSearch()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleSearch()
      }
    }
  }
</script>


