<template>
  <div class="upload">
    <input type="file" ref="input" style="display: none;">
    <div @click="change">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    methods: {
      change (e) {
        this.$refs.input.click()
        this.$refs.input.addEventListener('change', this.changeFile)
      },
      changeFile (e) {
        if (window.File && window.FileReader && window.FormData) {
          const reader = new FileReader()
          reader.readAsDataURL(this.$refs.input.files[0])
          reader.onload = (e) => {
              const src = e.target.result
              this.$emit('change', src)
          }
        } else {
          window.alert('File upload is not supported!')
        }
      }
    }
  }
</script>


