<template>
  <div class="sidebar">
    <div class="logo" :class="'logo-'+logoKey">{{logoText}}</div>
    <ul class="nav">
      <li v-for="(item, index) in data" 
        :key="index" :class="{'active': currentIndex == index}">
        <span class="title" @click="handleIndex(item, index)" :class="{open: item.open}">
          <i class="fa" :class="'icon-s-'+(index+iconIndex)"></i>{{item.title}}
          <i class="fa pull-right" :class="[item.open ? 'fa-angle-up' : 'fa-angle-down']"></i>
        </span>
        <router-link v-for="(sub, i) in item.children" :to="sub.path" :key="i" active-class="active" v-show="item.open">
          {{sub.title}}
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'v-sidebar',
    props: {
      iconIndex: {
        type: Number,
        default: 1
      }, 
      logoKey: {
        type: String,
        default: 'system'
      },
      logoText: {
        type: String,
        default: ''
      },
      data: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    data () {
      return {
        currentIndex: 0
      }
    },
    mounted () {
      this.data.forEach((item, i) => {
        item.children.find(key => key.path == this.$route.path) ? this.currentIndex = i : this.currentIndex = 0
      })
    },
    methods: {
      handleIndex (item, index) {
        this.currentIndex = index
        if (item.open == undefined) {
          this.$set(item, 'open', true)
        } else {
          item.open = !item.open
        }
      }
    }
  }
</script>

<style lang="stylus">
  @import '../stylus/config.styl'
  .sidebar [class*="icon-s"]
    height 20px
    width 20px
    display inline-block
    vertical-align top
    margin-top -2px
  .icon-s
    &-1
      background url('../img/icon-1.png') no-repeat center center
    &-2
      background url('../img/icon-2.png') no-repeat center center
    &-3
      background url('../img/icon-3.png') no-repeat center center
    &-4
      background url('../img/icon-4.png') no-repeat center center
    &-5
      background url('../img/icon-5.png') no-repeat center center
    &-6
      background url('../img/icon-6.png') no-repeat center center
    &-7
      background url('../img/icon-7.png') no-repeat center center
    &-8
      background url('../img/icon-8.png') no-repeat center center
    &-9
      background url('../img/icon-9.png') no-repeat center center
    &-10
      background url('../img/icon-10.png') no-repeat center center
  .sidebar 
    background url('../img/sidebar-bg.png') no-repeat center top
    margin-right 12px
    width _side_width
    .logo 
      color #abb3c3
      text-align center
      overflow hidden
      padding-top 75px
      margin-top 30px
      &-system 
        background url('../img/system-logo.png') no-repeat center top
      &-resource 
        background url('../img/resource-logo.png') no-repeat center top
      &-check 
        background url('../img/check-logo.png') no-repeat center top
    .nav 
      padding-top 30px 
      li
        color #abb3c3
        cursor pointer
        font-family SimHei
        font-size 16px 
        margin-bottom 47px
        &.active .open 
          color #fff
          .icon-s
            &-1
              background url('../img/icon-1-active.png') no-repeat center center
            &-2
              background url('../img/icon-2-active.png') no-repeat center center
            &-3
              background url('../img/icon-3-active.png') no-repeat center center
            &-4
              background url('../img/icon-4-active.png') no-repeat center center
            &-5
              background url('../img/icon-5-active.png') no-repeat center center
            &-6
              background url('../img/icon-6-active.png') no-repeat center center
            &-7
              background url('../img/icon-7-active.png') no-repeat center center
            &-8
              background url('../img/icon-8-active.png') no-repeat center center
            &-9
              background url('../img/icon-9-active.png') no-repeat center center
            &-10
              background url('../img/icon-10-active.png') no-repeat center center
        &.active a 
          color #fff
        .title 
          display block
          margin-bottom 20px
          height 20px
          padding 0 36px
          > i 
            padding-right 10px
        a 
          align-items center
          color #abb3c3
          display flex
          font-size 14px
          line-height 50px
          padding-left 80px
          &.active 
            background-color #1B253A
            position relative
            &:after 
              background-color #00E0C9
              border-radius 5px
              content ''
              height 100%
              left 0
              position absolute
              top 0
              width 3px
</style>


