<template>
  <div class="device-item">
    <div class="item" :class="[check ? 'checked' : '', type]">
      <div class="title">{{data.name}}</div>
      <div class="bottom">ip: {{data.ip}}</div>
      <div class="item-action">
        <i class="el-icon-edit"></i>
        <i class="el-icon-edit"></i>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'control-device',
    props: {
      data: {
        type: Object,
        default: {}
      }
    },
    computed: {
      check () {
        return this.data.deviceCheck
      },
      type () {
        switch (this.data.category) {
          case 0:
            return 'mc'
          case 1:
            return 'pc'
          case 2:
            return 'dr'
          case 3: 
            return ''
        }
      }
    }
  }
</script>

<style lang="stylus">
  .device-item
    box-sizing border-box
    display inline-block
    font-size 14px
    padding 0 10px 
    vertical-align top
    width 20%
    .item 
      height 220px
      padding 12px 12px
      margin-top 20px
      display flex 
      flex-direction column 
      justify-content space-between
      position relative
      &:hover 
        .item-action 
          display flex
      &-action 
        background-color #00b99c
        box-sizing border-box
        bottom 0
        height 40px 
        left 0
        position absolute
        width 100%
        display none 
        align-items center
        justify-content flex-end
        [class^='el']
          background-color #fff
          border 5px solid #fff 
          border-radius 50%
          color #00b99c
          cursor pointer
          margin-right 10px
      &.pc 
        background url('../img/device-pc-0.png') no-repeat center center #fff
        &.checked 
          background url('../img/device-pc-1.png') no-repeat center center #fff
      &.mc
        background url('../img/device-mc-0.png') no-repeat center center #fff
        &.checked 
          background url('../img/device-mc-1.png') no-repeat center center #fff
      &.dr
        background url('../img/device-dr-0.png') no-repeat center center #fff
        &.checked 
          background url('../img/device-dr-1.png') no-repeat center center #fff              
</style>


