<template>
  <div class="body">
    <div class="container">
      <div class="system-content">
        <el-tabs v-model="currentTab">
          <el-tab-pane :label="name+''" name="0">
            <v-search :search-text="name+''" style="margin-bottom: 20px;">
              <span slot="left">&nbsp;</span>
            </v-search>

            <v-table :data="table" :column="column" @update="handleAction"></v-table>
            <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page>
          </el-tab-pane>
          <el-tab-pane :label="name+''" name="1">
            <el-form ref="createRef" label-width="130px" label-position="left" style="width: 50%;margin: 0 auto;" :model="createModel">
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="name">
                <el-input v-model.trim="createModel.name"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
                <el-input type="textarea" v-model.trim="createModel.description"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="parentId">
                <el-input v-model.trim.number="createModel.parentId"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="categoryId">
                <el-select v-model.trim="createModel.categoryId" style="width: 100%;">
                  <el-option v-for="item in selectArr" :key="item.id" :label="item.name" :value="item.id">
                    <span style="float: left;">{{item.name}}</span>
                    <span style="float: right; color: #8492a6;">{{item.id}}</span>
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' class="btn" @click="handleCreate"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane :label="name+''" name="2" v-if="this.updateId">
            <el-form ref="updateRef" label-width="130px" label-position="left" style="width: 50%;margin: 0 auto;" :model="updateModel">
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="name">
                <el-input v-model.trim="updateModel.name"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
                <el-input type="textarea" v-model.trim="updateModel.description"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="parentId">
                <el-input v-model.trim.number="updateModel.parentId"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="categoryId">
                <el-select v-model.trim="updateModel.categoryId" style="width: 100%;">
                  <el-option v-for="item in selectArr" :key="item.id" :label="item.name" :value="item.id">
                    <span style="float: left;">{{item.name}}</span>
                    <span style="float: right; color: #8492a6;">{{item.id}}</span>
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' class="btn" @click="handleUpdate"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
  import {om, cate, oplog} from '../../api/index.js'
  export default {
    data () {
      return {
        name: '',
        column: om.column,
        table: [],
        total: 0,
        search: {
          q: '',
          pageSize: 10,
          pageNumber: 1
        },
        currentTab: '0',
        updateId: '',
        createModel: {
          name: '',
          description: '',
          parentId: '',
          categoryId: ''
        },
        updateModel: {
          name: '',
          description: '',
          parentId: '',
          categoryId: ''
        },
        selectArr: []
      }
    },
    mounted () {
      console.log(this.router)
      this.handleSearch()
      cate.search('', 1, 10).then(res => {
        if (res.data && res.data.status.code == 0) {
          this.selectArr = res.data.data
        }
      })
    },
    methods: {
      handleCreate () {
        this.$refs.createRef.validate(valid => {
          if (valid) {
            om.create(this.createModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.createRef.resetFields()
                this.handleSearch()
                this.$message({type: 'success', message: ''+this.name+'', duration: 1000})
                oplog.create(`${this.name}`)
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleUpdate () {
        this.$refs.updateRef.validate(valid => {
          if (valid) {
            om.put(this.updateId, this.updateModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.updateRef.resetFields()
                this.$message({type: 'success', message: ''+this.name+'', duration: 1000})
                this.updateId = ''
                this.currentTab = '0'
                this.handleSearch()
                oplog.create(`${this.name}`)
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleSearch (model) {
        if (model !== undefined) {
          this.search.q = model
        }
        om.search(this.search.q, this.search.pageNumber, this.search.pageSize).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.table = res.data.data
            this.total = parseInt(res.data.total)
            oplog.create(`${this.name}`)
          }
        })
      },
      handleAction (type, row) {
        if (type == 'delete') {
          om.delete(row.id).then(res => {
            if (res.data && res.data.status.code == '0') {
              // let index = this.table.indexOf(row)
              // this.table.splice(index, 1)
              this.handleSearch()
              oplog.create(`${this.name}`)
            }
          })
        } else {
          this.currentTab = '2'
          this.updateId = row.id
          for (var i in this.updateModel) {
            this.updateModel[i] = row[i]
          }
        }
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleSearch()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleSearch()
      }
    }
  }
</script>


