<template>
  <div class="home-light">
    <div class="container">
      <div class="home-light-header">
        <div class="home-light-header-item">
          <div class="home-light-header-item__title"></div>
          <div class="home-light-header-item__content">
            <div class="big" :class="[header.idcardAlarms.type ? 'u' : 'd']">{{header.comparisions.number}}<span class="small"></span></div>
            <span :class="[header.comparisions.type ? 'up' : 'down']">
            <i class="fa" :class="[header.comparisions.type ? 'fa-arrow-up' : 'fa-arrow-down']"></i>{{header.comparisions.abs}}%</span>
          </div>
        </div>
        <div class="home-light-header-item">
          <div class="home-light-header-item__title"></div>
          <div class="home-light-header-item__content">
            <div class="big" :class="[header.idcardAlarms.type ? 'u' : 'd']">{{header.totalAlarms.number}}<span class="small"></span></div>
            <span :class="[header.totalAlarms.type ? 'up' : 'down']">
            <i class="fa" :class="[header.totalAlarms.type ? 'fa-arrow-up' : 'fa-arrow-down']"></i>{{header.totalAlarms.abs}}%</span>
          </div>
        </div>
        <div class="home-light-header-item">
          <div class="home-light-header-item__title"></div>
          <div class="home-light-header-item__content">
            <div class="big" :class="[header.idcardAlarms.type ? 'u' : 'd']">{{header.portraitAlarms.number}}<span class="small"></span></div>
            <span :class="[header.portraitAlarms.type ? 'up' : 'down']">
            <i class="fa" :class="[header.portraitAlarms.type ? 'fa-arrow-up' : 'fa-arrow-down']"></i>{{header.portraitAlarms.abs}}%</span>
          </div>
        </div>
        <div class="home-light-header-item">
          <div class="home-light-header-item__title"></div>
          <div class="home-light-header-item__content">
            <div class="big" :class="[header.idcardAlarms.type ? 'u' : 'd']">{{header.idcardAlarms.number}}<span class="small"></span></div>
            <span :class="[header.idcardAlarms.type ? 'up' : 'down']">
            <i class="fa" :class="[header.idcardAlarms.type ? 'fa-arrow-up' : 'fa-arrow-down']"></i>{{header.idcardAlarms.abs}}%</span>
          </div>
        </div>
      </div>
      <div class="home-light-current">
        <div class="home-light-current__title"></div>
        <div class="home-light-current__content" style="padding-bottom: 30px;">
          <chart :options="current" style="width: 100%;"></chart>
        </div>
      </div>
      <div class="home-light-history">
        <div class="home-light-history-item">
          <div class="home-light-history-item__content">
            <div class="big">{{history.comparisions}} <span class="small"></span></div>
            <span class="title"></span>
          </div>
        </div>
        <div class="home-light-history-item">
          <div class="home-light-history-item__content">
            <div class="big">{{history.totalAlarms}} <span class="small"></span></div>
            <span class="title"></span>
          </div>
        </div>
        <div class="home-light-history-item">
          <div class="home-light-history-item__content">
            <div class="big">{{history.portraitAlarms}} <span class="small"></span></div>
            <span class="title"></span>
          </div>
        </div>
        <div class="home-light-history-item">
          <div class="home-light-history-item__content">
            <div class="big">{{history.idcardAlarms}} <span class="small"></span></div>
            <span class="title"></span>
          </div>
        </div>
      </div>
      <div class="home-light-stat">
        <div class="home-light-stat-item">
          <div class="home-light-stat-item__title"></div>
          <div class="home-light-stat-item__content">
            <div class="charts">
              <chart :options="deviceChart" style="width: 100%;"></chart>
            </div>
            <div class="text">
              <div class="text-item" v-for="(item, i) in deviceText" :key="item.name">
                <span class="text-desc" :style="{background: deviceColor[i]}"></span><span class="text-name">{{item.name}}</span><br>
                <span class="text-big">{{item.value}}</span><span class="text-small"></span>
              </div>
            </div>
          </div>
        </div>
        <div class="home-light-stat-item">
          <div class="home-light-stat-item__title"></div>
          <div class="home-light-stat-item__content">
            <div class="charts">
              <chart :options="controlChart" style="width: 100%;"></chart>
            </div>
            <div class="text">
              <div class="text-item" v-for="(item, i) in controlText" :key="item.name">
                <span class="text-desc" :style="{background: controlColor[i]}"></span><span class="text-name">{{item.name}}</span><br>
                <span class="text-big">{{item.value}}</span><span class="text-small"></span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import chart from 'vue-echarts'
  import {stat, device, control} from '../../api/index.js'
  export default {
    components: {
      chart
    },
    data () {
      return {
        header: {
          comparisions: {},
          totalAlarms: {},
          idcardAlarms: {},
          portraitAlarms: {}
        },
        history: {},
        current: {
          color:['#2fc7ac', '#d5f4ee'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              textStyle: {
                color: '#6481ab',
                padding: 0
              }
            },
          },
          legend: {
            bottom: '0%',
            textStyle: {
              color: '#719da7'
            },
            itemGap: 50,
            itemWidth: 14,
            data: ['', '']
          },
          grid:{
            x: 20,
            y: 40,
            x2: 20,
            y2: 60
          },
          xAxis: [
            {
              type: 'category',
              nameTextStyle: {
                color: '#719da7'
              },
              splitLine: {
                lineStyle: {
                  color: '#d9dde2'
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#d9dde2',
                }
              },
              axisLabel: {
                textStyle: {
                  color: '#719da7'
                }
              },
              data: [],
            }
          ],
          yAxis: [
            {
              type: 'value',
              splitLine: {
                lineStyle: {
                  color: '#d9dde2',
                  type: 'dashed'
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#d9dde2'
                }
              },
              axisLabel: {
                textStyle: {
                  color: '#719da7'
                }
              },
              name: '',
              nameTextStyle: {
                fontFamily: 'Microsoft YaHei Light',
                color: '#7d91a7'
              }
            }
          ],
          series: [
            {
              name: '',
              type: 'bar',
              data: [],
              stack: 'two',
            },
            {
              name: '',
              type: 'bar',
              data: [],
              stack: 'two',
            }
          ]
        },
        deviceText: [],
        controlText: [],
        controlColor: ['#55adeb', '#2687f3', '#3d51e4', '#2637b6'],
        deviceColor: ['#57c7ab', '#0c9f7a', '#008463', '#007155'],
        controlChart: {
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          color: ['#55adeb', '#2687f3', '#3d51e4', '#2637b6'],
          series: [
            {
              name:'',
              type:'pie',
              radius: ['50%', '70%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '30',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data:[]
            }
          ],
          animationDuration: 2000
        },
        deviceChart: {
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          series: [
            {
              name:'',
              type:'pie',
              radius: ['50%', '70%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '30',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data:[]
            }
          ],
          color: ['#57c7ab', '#0c9f7a', '#008463', '#007155'],
          animationDuration: 2000
        }
      }
    },
    mounted () {
      stat.search({
        started: this.$moment().subtract(9, 'days').format('YYYY-MM-DD'),
        ended: this.$moment().format('YYYY-MM-DD'),
        pageNumber: 1,
        pageSize: 10
      }).then(res => {
        if (res.data && res.data.status.code == 0) {
          res.data.data.map(item => {
            this.current.xAxis[0].data.push(item.date)
            this.current.series[0].data.push(item.totalAlarms)
            this.current.series[1].data.push(item.comparisions)
          })
          let total = res.data.data 
          let len = total.length
          let ye = total[len-2]
          let ye2 = total[len-3]
          for (var i in this.header) {
            if (len > 0) {
              this.header[i].number = total[len-1][i]
              if (len > 2) {
                this.header[i].abs = Math.abs(ye[i] - ye2[i]) / ye2[i] * 100
                this.header[i].type = ye[i] - ye2[i] > 0 ? true : false
              }
            }
          }
        }
      })
      stat.summary().then(res => {
        if (res.data && res.data.status.code == 0) {
          this.history = res.data.data
        }
      })
      control.all().then(res => {
        if (res.data && res.data.status.code == 0) {
          res.data.data.map(item => {
            this.controlText.push({name: this.controlToText(item.type), value: item.number})
            this.controlChart.series[0].data.push({name: this.controlToText(item.type), value: item.number})
          })
        }
      })
      device.all().then(res => {
        if (res.data && res.data.status.code == 0) {
          res.data.data.map(item => {
            this.deviceText.push({name: this.typeToText(item.category), value: item.number})
            this.deviceChart.series[0].data.push({name: this.typeToText(item.category), value: item.number})
          })
        }
      })
    },
    methods: {
      controlToText (type) {
        switch (type) {
          case 0: 
            return ''
          case 1:
            return ''
          case 2: 
            return ''
          case 3: 
            return ''
        }
      },
      typeToText (type) {
        switch (type) {
          case 'SC':
            return ''
          case 'TS':
            return ''
          case 'ZJ':
            return ''
          case 'LS':
            return ''
        }
      }
    }
  }
</script>

<style lang="stylus">
  bg_color = #fff 
  box_shadow = rgba(0,0,0,.1)
  .home-light 
    overflow hidden
    padding-bottom 30px
    flex 1
  .home-light-header 
    display flex
    height 183px
    margin-left -(home_header = 10px)
    margin-top 30px
    margin-right -(home_header)
    &-item 
      // background-color rgba(255,255,255,.5)
      background-color bg_color
      border 1px solid bg_color
      box-shadow 0px 0px 5px 3px box_shadow
      display flex
      flex-direction column
      margin 0 home_header
      flex 1
      &__title 
        color #3e6297
        font-family "Microsoft YaHei"
        font-size 18px 
        padding 30px 0
        text-align center
      &__content 
        flex 1
        text-align center
        .big 
          color #fff
          font-family "Microsoft YaHei Bold"
          font-size 45px
          &.u 
            color #E60012
          &.d
            color #11bb9d
        .small 
          font-size 14px
          margin-left 10px
        .up 
          font-size 20px
          color #E60012
          display block
          margin-top 20px
        .down 
          font-size 18px
          color #11bb9d
          display block
          margin-top 20px
      &__footer
        background-color #1E2D47
        color #96B0D4
        height 42px
        line-height 42px
        text-align center
  .home-light-current
    // background-color rgba(255,255,255,.5)
    background-color bg_color
    border 1px solid bg_color
    box-shadow 0px 0px 5px 3px box_shadow
    display flex 
    flex-direction column
    margin-top 20px
    padding 0 15px
    &__title 
      color #9CB7DC
      font-size 16px
      padding 30px 0
      text-align center
    &__content
      flex 1
  .home-light-history
    background-color bg_color
    border 1px solid bg_color
    box-shadow 0px 0px 5px 3px box_shadow
    display flex 
    height 150px
    margin-top 20px
    &-item 
      flex 1
      padding-left 80px
      position relative 
      &:not(:first-child):after
        border-left 1px dashed #94AFD2
        content ''
        left 0
        height 100px 
        position absolute 
        top 25px
        width 0px 
      &__content
        .big 
          color #467AE6
          font-size 30px
          font-family "Microsoft YaHei Light"
          margin-top 37px
          margin-bottom 14px
        .small 
          font-size 14px
        .title 
          color #3e6297
          font-size 16px
          font-family "Microsoft YaHei Light"
  .home-light-stat 
    display flex 
    height 440px 
    margin-top 20px
    margin-left -(home_header = 10px)
    margin-right -(home_header)
    margin-bottom 50px
    &-item 
      background-color bg_color
      border 1px solid bg_color
      box-shadow 0px 0px 5px 3px box_shadow
      display flex
      flex 1
      flex-direction column
      margin 0 home_header
      padding 0 15px
      &__title 
        color #3e6297
        font-size 18px
        padding 30px 0 0
        text-align center
      &__content
        display flex
        flex 1
        .charts 
          flex 1
          max-width 400px
        .text
          align-items center
          color #fff
          display flex 
          flex-direction column  
          justify-content center
          line-height 1.2
          width 158px
          &-desc 
            height 10px
            border-radius 50%
            position absolute 
            top 5px
            left -20px
            width 10px 
          &-item 
            margin 10px 0
            position relative
          &-name 
            color #748aaf
            font-family 'Microsoft YaHei'
            font-size 16px
          &-big 
            color #3e6297
            display inline-block
            font-family 'Microsoft YaHei Bold'
            font-size 30px
          &-small 
            color #748aaf
            margin-left 10px
</style>


