<template>
  <el-dialog :title="model.title" :visible.sync="model.show" size="tiny">
    <el-form :model="create" @submit.prevent="handleCreate" ref="createRef">
      <el-form-item v-for="(item, index) in model.inputs" :key="index" :rules="[{required: true, message: item.label + ''}]" prop="name">
        <template v-if="item.fetch">
          <el-autocomplete v-model.trim.number="create[item.prop]" :placeholder="item.label" v-if="item.type == 'number'" :fetch-suggestions="querySearch" style="width: 100%;"></el-autocomplete>
          <el-autocomplete v-model.trim="create[item.prop]" :placeholder="item.label" :fetch-suggestions="querySearch" style="width: 100%;" v-else></el-autocomplete>
        </template>
        <template v-else>
          <el-input v-model.trim.number="create[item.prop]" :placeholder="item.label" v-if="item.type == 'number'"></el-input>
          <el-input v-model.trim="create[item.prop]" :placeholder="item.label" v-else></el-input>
        </template>
      </el-form-item>
      <a @click="handleCreate" class="dialog-btn"></a>
    </el-form>
  </el-dialog>
</template>

<script>
  export default {
    name: 'v-dialog',
    props: {
      model: {
        type: Object,
        default: function () {
          return {}
        } 
      }
    },
    watch: {
      // 
      'model.show': function (val) {
        this.model.inputs.map(i => {
          this.create[i.prop] = this.model.model[i.prop]
        })
        clearTimeout(timer)
        let timer = setTimeout(() => {
          this.$refs.createRef.resetFields()
        }, 0)
      },
      'model.model': function (val) {
        if (val) {
          this.model.inputs.map(i => {
            this.create[i.prop] = val[i.prop]
          })
        }
      }
    },
    data () {
      return {
        create: {}
      }
    },
    methods: {
      handleCreate () {
        this.$refs.createRef.validate((valid) => {
          if (valid) {
            if (this.create.perms !== undefined) {
              this.create.perms = ["pf_create_user", "pf_edit_user"]
            }
            this.$emit('update', this.create)
          }
        })
      },
      querySearch (queryString, cb) {
        var restaurants = this.model.fetch
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (key) => {
          return (key.value.indexOf(queryString.toLowerCase()) === 0);
        };
      }
    }
  }
</script>

<style lang="stylus">
  .dialog-btn
    background-color #00B99C
    border-radius 4px
    box-sizing border-box
    border none
    color #fff
    cursor pointer
    display inline-block
    font-family MicrosfotYaHei 
    font-size 14px !important
    padding 11px 15px
    width 100%
    text-align center
</style>


