<template>
  <div class="system-content">
    <v-search search-text="" input-model="452225199205172557" @search="handleSearch">&nbsp;</v-search>

    <div class="bg-white">
      <v-table :data="table" :column="column" :action="[]">
        <el-table-column label="" prop="avatar" align="center" width="350px" slot="categories">
          <template scope="scope">
            <span v-for="(item, i) in scope.row.categories" :key="i" style="display: inline-block;margin: 0 5px;">{{item}}</span>
          </template>
        </el-table-column>
      </v-table>

      <!-- <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page> -->
    </div>
  </div>
</template>

<script>
  import {idcard} from '../../api/index.js'
  export default {
    data () {
      return {
        column: [
          {label: '', prop: 'name', width: '100px'},
          {label: '', prop: 'idNumber'},
          {label: '', prop: 'gender'},
          {label: '', prop: 'categories', type: 'slot'},
          {label: '', prop: 'address'}
        ],
        table: []
      }
    },
    methods: {
      handleSearch (model) {
        var reg = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/
        if (model == '') {
          this.$message({type: 'warning', message: ''})
          return 
        } else if (!reg.test(model)) {
          this.$message({type: 'warning', message: ''})
          return 
        }
        idcard(model).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.table.push(res.data.data)
          }
        })
      }
    }
  }
</script>


