<template>
  <div class="body">
    <div class="container">
      <div class="system-content">
        <v-search search-text="" @search="handleSearch">
          <el-select v-model="select" slot="prepend" placeholder="" style="width: 110px;">
            <el-option label="" value="q"></el-option>
            <el-option label="id" value="userId"></el-option>
            <el-option label="" value="userDisplayName"></el-option>
            <el-option label="" value="model"></el-option>
          </el-select>
        </v-search>

        <div class="flex">
          <div class="flex-item">
            <v-table :data="table" :column="column" :action="[]"></v-table>

            <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {oplog} from '../../api/index.js'
  export default {
    data () {
      return {
        column: [
          {label: '', prop: 'userDisplayName'},
          {label: '', prop: 'model'},
          {label: '', prop: 'createdAt'}
        ],
        table: [],
        total: 0,
        select: 'q',
        search: {
          pageSize: 10,
          pageNumber: 1
        }
      }
    },
    mounted () {
      this.handleSearch()
    },
    methods: {
      handleSearch (model) {
        let q = model == undefined ? '' : model
        let params = {}
        params[this.select] = q
        params['pageNumber'] = this.search.pageNumber
        params['pageSize'] = this.search.pageSize
        oplog.search(params).then(res => {
          this.table = res.data.data
          this.total = parseInt(res.data.total)
        })
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleSearch()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleSearch()
      }
    }
  }
</script>


