<template>
  <div class="body">
    <div class="container">
      <div class="system-content">
        <v-search btn-text="" search-text="" @btn="dialog.create.show = true" @search="handleSearch"></v-search>

        <div class="flex">
          <div class="flex-item">
            <div class="title">{{$route.query.name}}</div>
            <v-table :data="table" :column="column" @update="handleAction" @row-click="rowClick"></v-table>
          </div>
        </div>
        <v-dialog :model="dialog.create" @create="handleCreate"></v-dialog>
        <v-dialog :model="dialog.update"></v-dialog>
      </div>
    </div>
  </div>
</template>

<script>
  import {group} from '../../api/index.js'
  export default {
    data () {
      return {
        column: [
          {label: '', prop: 'name'},
          {label: '', prop: 'description'},
          {label: '', prop: 'created'},
          {label: '', prop: 'creator'}
        ],
        dialog: {
          create: {
            show: false,
            title: '',
            model: {},
            inputs: group.update.inputs
          },
          update: {
            show: false,
            title: '',
            model: {},
            inputs: group.update.inputs
          }
        },
        select: {
          val: '',
          options: []
        },
        table: [
          {name: 'Jack', description: "Jack's Group", created: '1504773699', creator: '0'}
        ]
      }
    },
    mounted () {
      this.handleInit()
    },
    methods: {
      handleInit () {
        group.users(this.$route.params.id).then(res => {
          this.table = res.data.data
        })
      },
      handleCreate (item) {
        group.create(item).then(res => {
          if (res.data && res.data.status.code == '0') {
            this.dialog.create.show = false
            this.handleInit()
          } else {
            this.$message({type: 'warning', message: ''})
          }
        })
      },
      handleSearch () {},
      handleAction (type, row) {
        if (type == 'delete') {
          group.delete(row.id).then().then(res => {
            var index = this.table.indexOf(row)
            this.table.splice(index, 1)
          })
        } else {
          this.dialog.update.show = true
          this.dialog.update.model = row
        }
      },
      rowClick (row) {
        this.$router.push({name: 'userGroupDetail', params: {id: row.id}})
      }
    }
  }
</script>


