<template>
  <div class="sidebar-simple">
    <router-link v-for="(item, i) in data" :to="item.path" :key="i" active-class="active">
      {{item.name}}
    </router-link>
  </div>
</template>

<script>
  export default {
    name: 'v-sidebar-simple',
    props: {
      name: '',
      routeId: '',
      data: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    data () {
      return {
        currentIndex: 0
      }
    }
  }
</script>

<style lang="stylus">
  @import '../stylus/config.styl'
  .sidebar-simple
    background url('../img/sidebar-bg.png') no-repeat center top
    margin-right 12px
    padding-top 18px
    width _side_width
    a 
      align-items center
      color #abb3c3
      display flex
      font-size 14px
      line-height 50px
      // justify-content center
      padding-left 50px
      &.active 
        background-color #1B253A
        position relative
        &:after 
          background-color #00E0C9
          border-radius 5px
          content ''
          height 100%
          left 0
          position absolute
          top 0
          width 3px
</style>


