<template>
  <div class="body">
    <div class="container system">
      <v-sidebar-simple :data="$store.state.sidebar"></v-sidebar-simple>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      routeId: ''
    },
    watch: {
      'routeId': function (val) {
        if (val) {
          this.$store.dispatch('handleSidebar', val)
        }
      }
    },
    mounted () {
      this.$store.dispatch('handleSidebar', this.routeId)
    }
  }
</script>


