<template>
  <header class="header">
    <section class="container">
      <section class="header-logo"><img :src="logo"></section>
      <section class="header-nav">
        <router-link :to="item.path" v-for="(item, index) in nav" :key="index" active-class="active">
          {{item.label}}
          <span class="header-nav-sub">
            <router-link :to="k.path" v-for="(k, i) in item.children" :key="i" active-class="active">{{k.text}}</router-link>
          </span>
        </router-link>
      </section>
      <section class="header-user" v-if="user">
        <div>{{user}}</div>
        <span style="color: #83dbe0;margin-left: 10px;cursor: pointer;" @click="handleLogout"></span>
      </section>
      <section class="header-user" v-else>
        <span style="color: #83dbe0;margin-left: 10px;cursor: pointer;" @click="handleLogin"></span>
      </section>
    </section>
  </header>
</template>

<script>
  import jwt from 'jwt-simple'
  export default {
    name: 'v-header',
    props: {
      logo: {
        type: String,
        default: ''
      },
      nav: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    data () {
      return {
        user: ''
      }
    },
    watch: {
      '$route': function () {
        if (this.user == '' || this.user == 'undefined') {
          this.handleCookieUser()
        }
      }
    },
    methods: {
      handleCookieUser () {
        let cookieJwt = this.$cookie.get('jwt')
        let key = 'TyCjiKGI9AhFstuVzSmwsXKyIbqR5iQAZqfd06vTJY5pJA5skClewZVOTtmw88KY'
        if (cookieJwt !== undefined) {
          var admin = jwt.decode(cookieJwt, key)
          this.user = admin.name
        } else {
          this.handleLogout()
        }
      },
      handleLogin () {
        let host = window.location.hostname
        let port = window.location.port
        window.location.href = 'http://' + host + ':9210/login?url=http://' + host + ':' + port + '/#/login'
      },
      handleLogout () {
        this.$cookie.remove('jwt')
        let host = window.location.hostname
        let port = window.location.port
        window.location.href = 'http://' + host + ':9210/login?url=http://' + host + ':' + port + '/#/login'
      }
    }
  }
</script>

<style lang="stylus">
  @import '../stylus/config.styl'
  .header 
    background-color _header_bg
    position fixed 
    left 0
    top 0
    width 100%
    z-index 100
    .container 
      display flex
      height _header_height
    &-logo 
      background url('../img/logo.png') no-repeat center 15px
      background-size auto 47px
      font-size 18px
      color #83dbe0
      font-weight bold
      line-height _header_height
      width 250px
    &-nav 
      flex 1
      padding-left 9px
      > a 
        color #859bb8
        display inline-block
        font-family SimHei 
        font-size 14px 
        line-height _header_height
        margin 0 16px
        padding 0 5px
        &.active 
          color #83dbe0
          position relative
          &:after 
            background-color #83dbe0 
            border-radius 5px
            content ''
            bottom 1px
            height 4px 
            left 0
            position absolute
            width 100%
    &-user 
      align-items center
      color #859bb8
      display flex
      font-family MicrosoftYaHei
      font-size 14px
      justify-content flex-end
      > i 
        font-size 22px
        margin-right 10px
</style>


