<template>
  <div class="repo-item">
    <div class="item">
      <div class="title">{{data.name ? data.name : ''}}</div>
      <div class="content">
        <div class="content-item" style="padding-right: 20px;">
          <template v-if="log">
            <div class="pic">
              <div class="pic-item"><img :src="log.faceImageUrl"></div>
              <div class="pic-item"><img :src="log.capturedFaceImageUrl"></div>
            </div>
            <div class="text">
              <div class="text-item green">
                <span></span>
                <span>{{log.compareThreshold}}%</span>
              </div>
              <div class="text-item">
                <span></span>
                <span>{{$moment(parseInt(log.created) * 1000).format('YYYY-MM-DD, h:mm:ss')}}</span>
              </div>
              <div class="text-item">
                <span></span>
                <span>{{log.idcardName}}</span>
              </div>
              <div class="text-item">
                <span></span>
                <span>{{log.idcardNo}}</span>
              </div>
              <div class="text-item">
                <span></span>
                <span>{{log.compareType == 0 ? '' : ''}}</span>
              </div>
            </div>
          </template>
          <template v-else>
            <div class="no-content"></div>
          </template>
        </div>
        <div class="content-item" style="padding-left: 20px;">
          <template v-if="alarm">
            <div class="pic">
              <div class="pic-item"><img :src="alarm.faceImageUrl" v-if="alarm.faceImageUrl"></div>
              <div class="pic-item" v-if="log"><img :src="log.capturedFaceImageUrl"></div>
            </div>
            <div class="text">
              <div class="text-item red">
                <span></span>
                <span>{{alarm.compareSimilarly}}%</span>
              </div>
              <div class="text-item">
                <span></span>
                <span>{{$moment(parseInt(alarm.updated) * 1000).format('YYYY-MM-DD, h:mm:ss a')}}</span>
              </div>
              <div class="text-item">
                <span></span>
                <span v-if="alarm.log">{{alarm.log.idcardName}}</span>
              </div>
              <div class="text-item">
                <span></span>
                <span v-if="alarm.log">{{alarm.log.idcardNo}}</span>
              </div>
              <div class="text-item">
                <span></span>
                <span v-if="alarm.log">{{alarm.log.compareType == 0 ? '' : ''}}</span>
              </div>
            </div>
          </template>
          <template v-else>
            <div class="no-content"></div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'repo-item',
    props: {
      data: {
        type: Object,
        default: {}
      }
    },
    computed: {
      log () {
        if (this.data.deviceLog) {
          return this.data.deviceLog
        }
      },
      alarm () {
        if (this.data.deviceAlarm) {
          return this.data.deviceAlarm
        }
      }
    }
  }
</script>

<style lang="stylus">
  .repo-item
    box-sizing border-box
    display inline-block
    font-size 14px
    padding 0 10px 
    vertical-align top
    width 50%
    .item 
      margin-top 20px
      .title 
        background-color #829AB9
        // background-color rgba(0,0,0,.6)
        color #fff 
        line-height 44px
        height 44px
        text-align center
      .content 
        background-color #fff 
        display flex 
        height 315px
        padding 20px
        .no-content 
          align-items center
          color #999
          font-size 30px
          display flex 
          height 100%
          justify-content center

        &-item 
          flex 1
          &:first-child 
            border-right 2px solid #D9E5F1
        .pic 
          background-color #fff
          display flex
          justify-content space-between
          position relative 
          &:after 
            content ''
            height 100px 
            width 0
            border-left 1px dashed #D9E5F1
            left 50%
            top 12px
            position absolute
          &-item 
            background-color #D9E5F1
            display flex 
            align-items center
            height 125px
            justify-content center
            width 110px 
            > img 
              max-width 100%
              max-height 100%
        .text 
          background-color #fff
          margin-top 13px
          &-item 
            display flex 
            justify-content space-between
            line-height 38px
            &:not(:first-child)
              border-top 1px dashed #F0F5F8
            &.green 
              color #00A48D
              font-size 18px
            &.red 
              color #FD0000
              font-size 18px
</style>



