<template>
  <div class="home">
    <div class="container">
      <div class="home-header">
        <div class="home-header-item">
          <div class="home-header-item__content">
            <div class="big">{{header.comparisions.number}}<span class="small"></span></div>
            <span :class="[header.comparisions.type ? 'up' : 'down']">
            <i class="fa" :class="[header.comparisions.type ? 'fa-arrow-up' : 'fa-arrow-down']"></i>{{header.comparisions.abs}}%</span>
          </div>
          <div class="home-header-item__footer"></div>
        </div>
        <div class="home-header-item">
          <div class="home-header-item__content">
            <div class="big">{{header.totalAlarms.number}}<span class="small"></span></div>
            <span :class="[header.totalAlarms.type ? 'up' : 'down']">
            <i class="fa" :class="[header.totalAlarms.type ? 'fa-arrow-up' : 'fa-arrow-down']"></i>{{header.totalAlarms.abs}}%</span>
          </div>
          <div class="home-header-item__footer"></div>
        </div>
        <div class="home-header-item">
          <div class="home-header-item__content">
            <div class="big">{{header.portraitAlarms.number}}<span class="small"></span></div>
            <span :class="[header.portraitAlarms.type ? 'up' : 'down']">
            <i class="fa" :class="[header.portraitAlarms.type ? 'fa-arrow-up' : 'fa-arrow-down']"></i>{{header.portraitAlarms.abs}}%</span>
          </div>
          <div class="home-header-item__footer"></div>
        </div>
        <div class="home-header-item">
          <div class="home-header-item__content">
            <div class="big">{{header.idcardAlarms.number}}<span class="small"></span></div>
            <span :class="[header.idcardAlarms.type ? 'up' : 'down']">
            <i class="fa" :class="[header.idcardAlarms.type ? 'fa-arrow-up' : 'fa-arrow-down']"></i>{{header.idcardAlarms.abs}}%</span>
          </div>
          <div class="home-header-item__footer"></div>
        </div>
      </div>
      <div class="home-current">
        <div class="home-current__title"></div>
        <div class="home-current__content" style="padding-bottom: 30px;">
          <chart :options="current" style="width: 100%;"></chart>
        </div>
      </div>
      <div class="home-history">
        <div class="home-history-item">
          <div class="home-history-item__content">
            <div class="big">{{history.comparisions}} <span class="small"></span></div>
            <span class="title"></span>
          </div>
        </div>
        <div class="home-history-item">
          <div class="home-history-item__content">
            <div class="big">{{history.totalAlarms}} <span class="small"></span></div>
            <span class="title"></span>
          </div>
        </div>
        <div class="home-history-item">
          <div class="home-history-item__content">
            <div class="big">{{history.portraitAlarms}} <span class="small"></span></div>
            <span class="title"></span>
          </div>
        </div>
        <div class="home-history-item">
          <div class="home-history-item__content">
            <div class="big">{{history.idcardAlarms}} <span class="small"></span></div>
            <span class="title"></span>
          </div>
        </div>
      </div>
      <div class="home-stat">
        <div class="home-stat-item">
          <div class="home-stat-item__title"></div>
          <div class="home-stat-item__content">
            <div class="charts">
              <chart :options="deviceChart" style="width: 100%;"></chart>
            </div>
            <div class="text">
              <div class="text-item" v-for="(item, i) in deviceText" :key="item.name">
                <span class="text-desc" :style="{background: deviceColor[i]}"></span><span class="text-name">{{item.name}}</span><br>
                <span class="text-big">{{item.value}}</span><span class="text-small"></span>
              </div>
            </div>
          </div>
        </div>
        <div class="home-stat-item">
          <div class="home-stat-item__title"></div>
          <div class="home-stat-item__content">
            <div class="charts">
              <chart :options="controlChart" style="width: 100%;"></chart>
            </div>
            <div class="text">
              <div class="text-item" v-for="(item, i) in controlText" :key="item.name">
                <span class="text-desc" :style="{background: controlColor[i]}"></span><span class="text-name">{{item.name}}</span><br>
                <span class="text-big">{{item.value}}</span><span class="text-small"></span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import chart from 'vue-echarts'
  import {stat, device, control} from '../../api/index.js'
  export default {
    components: {
      chart
    },
    data () {
      return {
        header: {
          comparisions: {},
          totalAlarms: {},
          idcardAlarms: {},
          portraitAlarms: {}
        },
        history: {},
        current: {
          color:['#BA0019','#00CEAF'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              textStyle: {
                color: '#1E2E45',
                padding: 0
              }
            },
          },
          legend: {
            bottom: '12%',
            textStyle: {
              color: '#fff'
            },
            data: ['', '']
          },
          grid:{
            x: 20,
            y: 20,
            x2:20,
            y2: 100,
          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              nameTextStyle: {
                color: '#fff'
              },
              splitLine: {
                lineStyle: {
                  color: '#1E2E45'
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#1E2E45',
                }
              },
              axisLabel: {
                textStyle: {
                  color: '#fff'
                }
              },
              data: [],
            }
          ],
          yAxis: [
            {
              type: 'value',
              splitLine: {
                lineStyle: {
                  color: '#1E2E45',
                  type: 'dashed'
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#1E2E45'
                }
              },
              axisLabel: {
                textStyle: {
                  color: '#fff'
                }
              },
            }
          ],
          series: [
            {
              name: '',
              type: 'bar',
              data: [],
              stack: 'two',
            },
            {
              name: '',
              type: 'bar',
              data: [],
              stack: 'two',
            }
          ]
        },
        deviceText: [],
        controlText: [],
        deviceColor: ['#55adeb', '#2687f3', '#3d51e4', '#2637b6'],
        controlColor: ['#57c7ab', '#0c9f7a', '#008463', '#007155'],
        controlChart: {
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            x: 'right',
            y: 'center',
            textStyle: {
              fontSize: '20'
            },
            data:['','']
          },
          color: ['#57c7ab', '#0c9f7a', '#008463', '#007155'],
          series: [
            {
              name:'',
              type:'pie',
              radius: ['40%', '70%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '30',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data:[]
            }
          ],
          animationDuration: 2000
        },
        deviceChart: {
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            x: 'right',
            y: 'center',
            textStyle: {
              fontSize: '20'
            },
            data:['','']
          },
          series: [
            {
              name:'',
              type:'pie',
              radius: ['40%', '70%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '30',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data:[]
            }
          ],
          color: ['#55adeb', '#2687f3', '#3d51e4', '#2637b6'],
          animationDuration: 2000
        }
      }
    },
    mounted () {
      stat.search({
        started: this.$moment().subtract(9, 'days').format('YYYY-MM-DD'),
        ended: this.$moment().format('YYYY-MM-DD'),
        pageNumber: 1,
        pageSize: 10
      }).then(res => {
        if (res.data && res.data.status.code == 0) {
          res.data.data.map(item => {
            this.current.xAxis[0].data.push(item.date)
            this.current.series[0].data.push(item.totalAlarms)
            this.current.series[1].data.push(item.comparisions)
          })
          let total = res.data.data 
          let len = total.length
          let ye = total[len-2]
          let ye2 = total[len-3]
          for (var i in this.header) {
            if (len > 0) {
              this.header[i].number = total[len-1][i]
              if (len > 2) {
                this.header[i].abs = Math.abs(ye[i] - ye2[i]) / ye2[i] * 100
                this.header[i].type = ye[i] - ye2[i] > 0 ? true : false
              }
            }
          }
        }
      })
      stat.summary().then(res => {
        if (res.data && res.data.status.code == 0) {
          this.history = res.data.data
        }
      })
      control.all().then(res => {
        if (res.data && res.data.status.code == 0) {
          res.data.data.map(item => {
            this.controlText.push({name: this.controlToText(item.type), value: item.number})
            this.controlChart.series[0].data.push({name: this.controlToText(item.type), value: item.number})
          })
        }
      })
      device.all().then(res => {
        if (res.data && res.data.status.code == 0) {
          res.data.data.map(item => {
            this.deviceText.push({name: this.typeToText(item.category), value: item.number})
            this.deviceChart.series[0].data.push({name: this.typeToText(item.category), value: item.number})
          })
        }
      })
    },
    methods: {
      controlToText (type) {
        switch (type) {
          case 0: 
            return ''
          case 1:
            return ''
          case 2: 
            return ''
          case 3: 
            return ''
        }
      },
      typeToText (type) {
        switch (type) {
          case 'SC':
            return ''
          case 'TS':
            return ''
          case 'ZJ':
            return ''
          case 'LS':
            return ''
        }
      }
    }
  }
</script>

<style lang="stylus">
  .home 
    background url('../../shared/img/home-bg.jpg') no-repeat center top
    overflow hidden
    padding-bottom 30px
    flex 1
  .home-header 
    display flex
    height 183px
    margin-left -(home_header = 10px)
    margin-top 30px
    margin-right -(home_header)
    &-item 
      // background-color rgba(255,255,255,.5)
      background-color #031122
      border 1px solid #14253C
      box-shadow 0px 0px 5px 3px #14253C inset
      display flex
      flex-direction column
      margin 0 home_header
      flex 1
      &__content 
        flex 1
        text-align center
        .big 
          color #fff
          font-size 54px
          margin-top 26px
        .small 
          font-size 14px
          margin-left 10px
        .up 
          font-size 20px
          color #E60012
          display block
          margin-top 20px
        .down 
          font-size 18px
          color #2FC7AC
          display block
          margin-top 20px
      &__footer
        background-color #1E2D47
        color #96B0D4
        height 42px
        line-height 42px
        text-align center
  .home-current
    // background-color rgba(255,255,255,.5)
    background-color #031122
    border 1px solid #14253C
    box-shadow 0px 0px 5px 3px #14253C inset
    display flex 
    flex-direction column
    height 415px
    margin-top 20px
    padding 0 15px
    &__title 
      border-bottom 1px solid #2D3E5A
      color #9CB7DC
      font-size 16px
      padding 15px 0
    &__content
      flex 1
  .home-history
    background-color #031122
    border 1px solid #14253C
    box-shadow 0px 0px 5px 3px #14253C inset
    display flex 
    height 150px
    margin-top 20px
    &-item 
      flex 1
      padding-left 65px
      position relative 
      &:not(:first-child):after
        border-left 1px dashed #94AFD2
        content ''
        left 0
        height 100px 
        position absolute 
        top 25px
        width 0px 
      &__content
        .big 
          color #467AE6
          font-size 34px
          margin-top 37px
          margin-bottom 14px
        .small 
          font-size 14px
        .title 
          color #94AFD2
  .home-stat 
    display flex 
    height 440px 
    margin-top 20px
    margin-left -(home_header = 10px)
    margin-right -(home_header)
    &-item 
      background-color #031122
      border 1px solid #14253C
      box-shadow 0px 0px 5px 3px #14253C inset
      display flex
      flex 1
      flex-direction column
      margin 0 home_header
      padding 0 15px
      width 50%
      &__title 
        border-bottom 1px solid #2D3E5A
        color #9CB7DC
        font-size 16px
        padding 15px 0
      &__content
        display flex
        flex 1
        .charts
          flex 1
          max-width 400px
        .text
          align-items center
          color #fff
          display flex 
          flex-direction column  
          justify-content center
          line-height 1.2
          width 158px
          &-desc 
            height 10px
            border-radius 50%
            position absolute 
            top 5px
            left -20px
            width 10px 
          &-item 
            margin 5px 0
            position relative
          &-name 
            font-size 18px
          &-big 
            display inline-block
            font-size 32px
</style>


