<template>
  <div class="search">
    <template v-if="$slots.left">
      <slot name="left"></slot>
    </template>

    <div class="search-bar" >
      <el-input :placeholder="''+searchText+''" v-model="inputModel" :style="{width: $slots.prepend ? '340px' : '240px'}">
        <template v-if="$slots.prepend" slot="prepend">
          <slot name="prepend"></slot>
        </template>
      </el-input>
      <a class="search-btn pull-right" style="margin-left: 5px;" @click="handleBtnSearch"></a>
    </div>
    
  </div>
</template>

<script>
  export default {
    name: 'v-search',
    props: {
      searchText: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        inputModel: ''
      }
    },
    methods: {
      handleBtnSearch () {
        this.$emit('search', this.inputModel)
      }
    }
  }
</script>

<style lang="stylus">
  @import '../stylus/config.styl'
  .search
    align-items center
    background-color #fff
    display flex
    font-size 14px
    justify-content space-between
    height 62px
    margin-bottom 12px
    padding 0 10px 0 20px
    &-btn 
      background-color #00B99C
      border-radius 4px
      border none
      color #fff
      cursor pointer
      display inline-block
      font-family MicrosfotYaHei 
      font-size 14px !important
      padding 11px 15px
      &:hover
        background-color lighten(#00B99C, 5)
        color #fff
      &:active,&:visited
        background-color lighten(#00B99C, 5)
        color #fff !important
    &-bar 
      display flex
      justify-content space-between
      height 36px
      input 
        // background-color #f1f6f9
        height 36px
        color #999
        // font-family MicrosoftYaHeiLight
        // font-size 12px
        outline none
        &::-webkit-input-placeholder
          // color #999
          // font-family MicrosoftYaHeiLight
          // font-size 12px
      .el-input-group__append
        background-color #EAEFF2
        font-size 16px
</style>


