<template>
  <div class="body">login</div>
</template>

<script>
  export default {
    mounted () {
      this.$cookie.set('jwt', this.$route.query.jwt)
      if (localStorage.getItem('router-back')) {
        location.href = localStorage.getItem('router-back')
        localStorage.removeItem('router-back')
      } else {
        this.$router.push('home')
      }
    }
  }
</script>


