<script>
  export default {
    data () {
      return {
        navs: [
          {
            title: '',
            children: [
              {title: '',path: '/resource/controlCog'}
            ]
          },
          {
            title: '',
            children: [
              {title: '',path: '/resource/device'},
              {title: '',path: '/resource/control'}
            ]
          },
          {
            title: '',
            children: [
              {title: '',path: '/resource/faceset'}
            ]
          }
        ]
      }
    }
  }
</script>


