<template>
  <div class="system-content" style="position: relative;">
    <div class="control-tag">
      <span class="title"> ></span>
      <span class="tag" v-for="(item,i) in tags" :key="item.name" :class="{active: tagIndex == i}" @click="handleTagIndex($event, item)">
        {{item.description ? item.description : ''}}
      </span>
    </div>
    <v-search :search-text="''" style="margin-bottom: 0;" @search="handleSearch">
    </v-search>
    <div class="control-btn">
      <span class="btn-text" v-for="(item, i) in checkgroup" :class="[checkIndex == i ? 'active' : '']" @click="handleCheck(item)">{{item.label}}</span>
    </div>
    <div class="control-list">
      <control-item v-for="(item, i) in table" :key="i" :data="item"></control-item>
    </div>

    <div>
      <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import controlItem from '../../shared/components/control-device.vue'
  import {om, device, control, deviceControl, deepCopy} from '../../api/index.js'
  export default {
    components: {
      controlItem
    },
    data () {
      return {
        tagIndex: 1,
        controlId: '',
        checkIndex: 2,
        checkgroup: [
          {label: '', type: '1'},
          {label: '', type: '0'},
          {label: '', type: '-1'}
        ],
        tree: [],
        table: [],
        unitIds: [],
        deviceIds: [],
        tags: [],
        devices: [],
        deviceControls: [],
        total: 0,
        search: {
          q: '',
          pageNumber: 1,
          pageSize: 10
        },
      }
    },
    created () {
      this.handleControl()
    },
    mounted () {
      this.handleTable()
      this.handleHttpAll()
    },
    methods: {
      handleCheck (i) {
        let index = this.checkgroup.indexOf(i)
        this.checkIndex = index
        this.handleTable()
      },
      handleBtnSearch (q) {
        om.search(q, 1, 10).then(res => {
          this.tree = res.data.data
        })
      },
      handleControl () { // 
        control.search({q: '', pageNumber: 1, pageSize: 10}).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.tags = res.data.data
            this.controlId = this.tags[this.tagIndex].id
            console.log(this.controlId)
          }
        })
      },
      handleDeviceControl () { // 
        deviceControl.search({controlId: this.controlId,pageNumber: 1, pageSize: 1000}).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.deviceControls = res.data.data
            if (this.checkIndex == 0) {
              this.table = deepCopy(this.deviceControls)
            }
          }
        })
      },
      handleDevice () { // 
        device.search({q: '', pageNumber: 1, pageSize: 1000}).then(dev => {
          if (dev.data && dev.data.status.code == 0) {
            this.devices = dev.data.data
          }
        })
      },
      handleSearch (model) {
        this.search.q = model
        this.handleTable()
      },
      handleHttpAll () {
        axios.all([device.search({q: '', pageNumber: 1, pageSize: 1000}), deviceControl.search({controlId: '933596429887209472',pageNumber: 1, pageSize: 1000})]).then(axios.spread(function (acct, perms) {
          console.log(acct, perms)
        }))
      },
      handleTable () {
        device.search({q: this.search.q, pageNumber: 1, pageSize: 1000}).then(dev => {
          if (dev.data && dev.data.status.code == 0) {
            this.devices = dev.data.data
            deviceControl.search({controlId: this.controlId,pageNumber: 1, pageSize: 1000}).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.deviceControls = res.data.data
                // 
                this.devices.map(item => {
                  this.deviceControls.map(key => {
                    if (item.id == key.deviceId) {
                      if (item.deviceCheck == undefined) {
                        this.$set(item, 'deviceCheck', true)
                      }
                    } else {
                      if (item.deviceCheck == undefined) {
                        this.$set(item, 'deviceCheck', false)
                      }
                    }
                  })
                })
                let devCopy = deepCopy(this.devices)
                let from = (this.search.pageNumber - 1)* this.search.pageSize
                let to = this.search.pageSize

                this.table = []
                if (this.checkIndex == 0) {
                  let devCheck = devCopy.filter(p => p.deviceCheck)
                  this.total = devCheck.length
                  this.table = devCheck.splice(from, to)
                } else if (this.checkIndex == 1) {
                  let unCheck = devCopy.filter(p => !p.deviceCheck)
                  this.total = unCheck.length
                  this.table = unCheck.splice(from, to)
                } else if (this.checkIndex == 2) {
                  this.total = devCopy.length
                  this.table = devCopy.splice(from, to)
                }
              }
            })
          }
        })
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleTable()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleTable()
      },
      handleTagIndex (e, item) {
        let index = this.tags.indexOf(item)
        this.tagIndex = index 
        this.controlId = item.id
        this.handleTable()
      }
    }
  }
</script>

<style lang="stylus">
  .control-tag 
    background-color #fff 
    padding 12px 10px 0 20px
    span 
      display inline-block 
      vertical-align top
      line-height 24px
      margin-right 20px
      font-size 14px
      color #666
    .tag
      background-color #fff 
      border 1px solid #999
      border-radius 4px
      box-sizing border-box
      color #333
      font-size 12px
      cursor pointer
      padding 0 5px 
      line-height 22px 
      height 24px
      &.active 
        border-color #00b99c
        color #00b99c
  .control-list
    font-size 0
    margin-left -10px
    margin-right -10px
    padding-bottom 20px
  .control-btn
    background-color #fff 
    padding 12px 10px 12px 20px
    .btn-text 
      cursor pointer
      height 36px
      font-size 14px
      display inline-block
      box-sizing border-box
      color #00B99C
      padding 11px 15px
      margin-right 20px
      &.active 
        border-radius 20px
        background-color #00B99C
        color #fff
</style>



