<template>
  <div class="body">
    <div class="container">
      <div class="system-content">
      <v-search btn-text="" search-text="" @search="handleSearch"></v-search>

      <div class="flex">
        <div class="flex-item" style="width: 220px;">
          <div class="title"></div>
          <div v-for="(item, i) in table" :key="i" style="padding-left: 50px;">
            <el-button type="text" @click="handlePerms(item.id)">{{item.name}}</el-button>
          </div>
        </div>
        <div class="flex-item flex-fix">
          <div v-for="(perm, index) in perms" :key="index">
            <div class="title">{{perm.title}}</div>
            <el-checkbox v-for="(item, i) in perm.perms" :label="item.name" :key="item.id" @change="handlePermItem($event, perm.id, item)">{{item}}</el-checkbox>
          </div>
        </div>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {user, service, oplog} from '../../api/index.js'
  export default {
    data () {
      return {
        name: '',
        table: [],
        ownerId: '',
        perms: [],
        total: 0,
        search: {
          q: '',
          pageSize: 10,
          pageNumber: 1
        }
      }
    },
    mounted () {
      this.handleSearch()
      service.search('', 1, 10).then(res => {
        res.data.data.map(item => {
          this.perms.push({
            title: item.name,
            id: item.id,
            perms: item.perms
          })
        })
      })
    },
    methods: {
      handleSearch (model) {
        if (model !== undefined) {
          this.search.q = model
        }
        user.search(this.search.q, this.search.pageNumber, this.search.pageSize).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.table = res.data.data
            this.total = parseInt(res.data.total)
            oplog.create(`${this.name}`)
          }
        })
      },
      handlePerms (id) {
        this.ownerId = id
        service.granted(id)
      },
      handlePermItem (e, id, key) {
        if (e.target.checked) {
          service.grant({
            ownerId: this.ownerId,
            servicePerms: [{
              serviceId: id,
              perm: key
            }]
          }).then(res => {
            this.$message({type: 'success', message: ''})
            oplog.create(`${this.name}`)
          })
        } else {
          service.revoke({
            ownerId: this.ownerId,
            servicePerms: [{
              serviceId: id,
              perm: key
            }]
          }).then(res => {
            this.$message({type: 'success', message: ''})
            oplog.create(`${this.name}`)
          })
        }
      }
    }
  }
</script>


