import fetch from '../../shared/api/fetch.js'

class Fetch {
  constructor (url) {
    this.url = url
    this.get = this.get.bind(this)
    this.create = this.create.bind(this)
    this.search = this.search.bind(this)
    this.update = this.update.bind(this)
    this.delete = this.delete.bind(this)
  }
  get (id) {
    return fetch.get(this.url+'/'+id)
  }
  create (params) {
    return fetch.post(this.url, params)
  }
  search (params) {
    return fetch.post(this.url + '/search', params)
  }
  update (id, params) {
    return fetch.put(this.url+'/'+id, params)
  }
  delete (id) {
    return fetch.delete(this.url+'/'+id)
  }
}

class AlarmsFetch extends Fetch {
  constructor () {
    super('alarms')
  }
}

class ControlsFetch extends Fetch {
  constructor () {
    super('controls')
  }
  all () {
    return fetch.get('controls/stats/all')
  }
}

class Devices extends Fetch {
  constructor () {
    super('devices')
    this.all = this.all.bind(this)
  }

  all () {
    return fetch.get('devices/stats/all')
  }
}

class DevicesControls extends Fetch {
  constructor () {
    super('device_controls')
  }
}

class LogsFetch extends Fetch {
  constructor () {
    super('logs')
  }
}

class Stats {
  constructor () {

  }
  //  put
  update () {

  }
  // 
  search (params) {
    return fetch.post('stats/devicelog/search', params)
  }
  // 
  summary () {
    return fetch.get('stats/devicelog/summary')
  }
}



export const alarm = new AlarmsFetch()
export const log = new LogsFetch()
export const device = new Devices()
export const control = new ControlsFetch()
export const deviceControl = new DevicesControls()
export const stat = new Stats()
export * from '../../shared/api/index.js'


