import axios from 'axios'
import { Message } from 'element-ui'
import qs from 'qs'
import cookie from 'js-cookie'

const service = axios.create({
  // baseURL: 'http://10.1.1.114:9210/v2/',
  baseURL: window.apiHost,
  // transformRequest: [function (data) {
  //   return qs.stringify(data)
  // }],
  // headers: {'Content-Type': 'application/x-www-form-urlencoded'},
  // withcredentials: true,
  timeout: 5000
})

// axios.defaults.headers = {
//   'Content-Type': 'application/x-www-form-urlencoded'
// }
// axios.defaults.transformRequest = [function (data) {
//   return qs.stringify(data)
// }]


service.interceptors.request.use(config => {
  // config.headers['authorization'] = 'JWT '+'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1SWQiOjkxMzYzMTAyMDIxNTk1OTU1MiwibmFtZSI6ImZpenoiLCJleHAiOjE1MTEzMTc3NTMsImlzcyI6Im5leHQtc3RhZ2UuY29tLmNuIn0.tz1qcfFczlRkzgo88YhMOww2TOva9HUib_6i_czcbUk'
  // console.log(cookie.get('jwt'))
  if (cookie.get('jwt') !== undefined) {
    config.headers['authorization'] = 'JWT ' + cookie.get('jwt')
  }
  return config
}, error => {
  console.log(error)
  console.log('')
  Promise.rejcet(error)
})

service.interceptors.response.use(response => response, error => {
  console.log('err' + error)// for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 1000
    })
    return Promise.reject(error)
})

export default service


