<template>
  <div class="system-content">
    <search-tree @search="handleBtnSearch" :tree-data="tree" @update="handleTreeUpdate" :show-checkbox="false">
      <div class="tree-footer" slot="footer">
        <span class="btn-text" v-for="(item, i) in checkgroup" :class="[checkIndex == i ? 'active' : '']" @click="handleCheck(item)">{{item.label}}</span>
      </div>
    </search-tree>

    <div class="device-list">
      <device-item v-for="(item, i) in table" :key="i" :data="item"></device-item>
    </div>

    <div>
      <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page>
    </div>
  </div>
</template>

<script>
  import deviceItem from '../../shared/components/device-item.vue'
  import {om, device, control, deviceControl} from '../../api/index.js'
  export default {
    components: {
      deviceItem
    },
    data () {
      return {
        checkIndex: 0,
        checkgroup: [
          {label: '', type: '1'},
          {label: '', type: '0'}
        ],
        tree: [],
        table: [],
        total: 0,
        search: {
          unitId: '',
          pageSize: 10,
          pageNumber: 1,
          status: 1
        }
      }
    },
    mounted () {
      this.handleSearch()
    },
    methods: {
      handleCheck (i) {
        let index = this.checkgroup.indexOf(i)
        this.checkIndex = index
        this.search.status = parseInt(i.type)
        this.handleSearch()
      },  
      handleSearch () {
        let params = this.search.unitId == '' ? {q: '', pageNumber: this.search.pageNumber, pageSize: this.search.pageSize, status: this.search.status}
          : {q: '', pageNumber: this.search.pageNumber, pageSize: this.search.pageSize, unitId: this.search.unitId, status: this.search.status}
        device.search(params).then(res => {
          this.table = res.data.data
          this.total = parseInt(res.data.total)
        })
      },
      handleBtnSearch (q) {
        om.search(q, 1, 10).then(res => {
          this.tree = res.data.data
        })
      },
      handleTreeUpdate (data) {
        if (data.length > 0) {
          this.search.unitId = data[0].id  
        } else {
          this.search.unitId = ''
        }
        this.handleSearch()
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleSearch()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleSearch()
      },
      jsonTree (json, id, pid, children) {
        var r = [], hash = {}, i = 0, j = 0, len = json.length
        for (;i < len; i++) {
          hash[json[i][id]] = json[i]
        }
        for (;j < len; j++) {
          var val = json[j], hval = hash[val[pid]]
          if (hval) {
            !hval[children]&&(hval[children] = [])
            hval[children].push(val)
          } else {
            r.push(val)
          }
        }
        return r
      }
    }
  }
</script>

<style lang="stylus">
  .device-list
    font-size 0
    margin-left -10px
    margin-right -10px
    padding-bottom 20px
</style>


