<template>
  <div class="system-content">
    <el-tabs v-model="currentTab">
      <!--  -->
      <el-tab-pane :label="name+''" name="0">
        <v-search search-text="" style="margin-bottom: 20px;">
          <span slot="left">&nbsp;</span>
        </v-search>
        <v-table :column="column" :data="table" @update='handleAction'></v-table>

        <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page>
      </el-tab-pane>
      <!--  -->
      <el-tab-pane :label="name+''" name="1">
        <el-form ref="createRef" label-width="100px" label-position="left" style="width: 50%;margin: 0 auto;" :model="createModel">
          <el-form-item label="">
            <el-select v-model="createModel.controlType" style="width: 100%;">
              <el-option label="" :value="0"></el-option>
              <el-option label="" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.version">
            <el-input v-model.trim='createModel.service.version'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.protocol">
            <el-input v-model.trim='createModel.service.protocol'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.address">
            <el-input v-model.trim='createModel.service.address'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.method">
            <el-input v-model.trim='createModel.service.method'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.authorize">
            <el-input v-model.trim='createModel.service.authorize'></el-input>
          </el-form-item>
          <el-form-item label="Token" :rules='[{required: true, message: "Token"}]' prop="service.token">
            <el-input v-model.trim='createModel.service.token'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.username">
            <el-input v-model.trim='createModel.service.username'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.password">
            <el-input v-model.trim='createModel.service.password' type="password"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="createModel.blacklist.on"></el-switch>
          </el-form-item>
          <el-form-item label-width="0px" v-show="createModel.blacklist.on == 1">
            <el-row>
              <el-col :span="10">
                <el-form-item label="">
                  <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="createModel.blacklist.alarmOn"></el-switch>
                </el-form-item>
              </el-col>
              <el-col :span="10" :offset="4">
                <el-form-item label="">
                  <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="createModel.blacklist.gateOn"></el-switch>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="ID" v-show="createModel.blacklist.on == 1">
            <el-input v-model.trim='createModel.blacklist.ids'></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="createModel.whitelist.on"></el-switch>
          </el-form-item>
          <el-form-item label-width="0px" v-show="createModel.whitelist.on == 1">
            <el-row>
              <el-col :span="10">
                <el-form-item label="">
                  <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="createModel.whitelist.alarmOn"></el-switch>
                </el-form-item>
              </el-col>
              <el-col :span="10" :offset="4">
                <el-form-item label="">
                  <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="createModel.whitelist.gateOn"></el-switch>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="ID" v-show="createModel.whitelist.on == 1">
            <el-input v-model="createModel.whitelist.ids"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-slider v-model="createModel.compare_threshold"></el-slider>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
            <el-input v-model="createModel.description"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="createModel.status"></el-switch>
          </el-form-item>
          <el-form-item>
            <el-button type='primary' class="btn" @click="handleCreate"></el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <!--  -->
      <el-tab-pane :label="name+''" name="2" v-if="this.updateId">
        <el-form ref="updateRef" label-width="100px" label-position="left" style="width: 50%;margin: 0 auto;" :model="updateModel">
          <el-form-item label="">
            <el-select v-model="updateModel.controlType" style="width: 100%;">
              <el-option label="" :value="0"></el-option>
              <el-option label="" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.version">
            <el-input v-model.trim='updateModel.service.version'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.protocol">
            <el-input v-model.trim='updateModel.service.protocol'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.address">
            <el-input v-model.trim='updateModel.service.address'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.method">
            <el-input v-model.trim='updateModel.service.method'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.authorize">
            <el-input v-model.trim='updateModel.service.authorize'></el-input>
          </el-form-item>
          <el-form-item label="Token" :rules='[{required: true, message: "Token"}]' prop="service.token">
            <el-input v-model.trim='updateModel.service.token'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.username">
            <el-input v-model.trim='updateModel.service.username'></el-input>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="service.password">
            <el-input v-model.trim='updateModel.service.password' type="password"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="updateModel.blacklist.on"></el-switch>
          </el-form-item>
          <el-form-item label-width="0px" v-show="updateModel.blacklist.on == 1">
            <el-row>
              <el-col :span="10">
                <el-form-item label="">
                  <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="updateModel.blacklist.alarmOn"></el-switch>
                </el-form-item>
              </el-col>
              <el-col :span="10" :offset="4">
                <el-form-item label="">
                  <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="updateModel.blacklist.gateOn"></el-switch>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="ID" v-show="updateModel.blacklist.on == 1">
            <el-input v-model.trim='updateModel.blacklist.ids'></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="updateModel.whitelist.on"></el-switch>
          </el-form-item>
          <el-form-item label-width="0px" v-show="updateModel.whitelist.on == 1">
            <el-row>
              <el-col :span="10">
                <el-form-item label="">
                  <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="updateModel.whitelist.alarmOn"></el-switch>
                </el-form-item>
              </el-col>
              <el-col :span="10" :offset="4">
                <el-form-item label="">
                  <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="updateModel.whitelist.gateOn"></el-switch>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="ID" v-show="updateModel.whitelist.on == 1">
            <el-input v-model="updateModel.whitelist.ids"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-slider v-model="updateModel.compare_threshold"></el-slider>
          </el-form-item>
          <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
            <el-input v-model="updateModel.description"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch on-text="" off-text="" :on-value="1" :off-value="0" v-model="updateModel.status"></el-switch>
          </el-form-item>
          <el-form-item>
            <el-button type='primary' class="btn" @click="handleUpdate"></el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import {control, oplog} from '../../api/index.js'
  export default {
    data () {
      return {
        // table 
        name: '',
        column: [
          {label: 'ID', prop: 'id'},
          {label: '', prop: 'controlType'},
          {label: '', prop: 'description'},
          {label: '', prop: 'status'},
          {label: '', prop: 'created'},
          {label: '', prop: 'updated'}
        ],
        table: [],
        total: 0,
        search: {
          q: '',
          pageSize: 10,
          pageNumber: 1
        },
        currentTab: '0',
        updateId: '',
        // create 
        createModel: {
          controlType: 0,
          service: {
            version: '', // string
            protocol: '', // string
            address: '', // string
            method: '', // string
            authorize: '', // string
            token: '', // string
            username: '',  // string
            password: '' // string
          },
          blacklist: {
            on: 0, // number
            ids: '', // string
            alarmOn: 0, // number
            gateOn: 0 // number
          },
          whitelist: {
            on: 0, // number
            ids: '', // string
            alarmOn: 0, // number
            gateOn: 0 // number
          },
          compare_threshold: 0,
          description: '',
          status: ''
        },
        // update 
        updateModel: {
          controlType: 0,
          service: {
            version: '', // string
            protocol: '', // string
            address: '', // string
            method: '', // string
            authorize: '', // string
            token: '', // string
            username: '',  // string
            password: '' // string
          },
          blacklist: {
            on: 0, // number
            ids: '', // string
            alarmOn: 0, // number
            gateOn: 0 // number
          },
          whitelist: {
            on: 0, // number
            ids: '', // string
            alarmOn: 0, // number
            gateOn: 0 // number
          },
          compare_threshold: 0,
          description: '',
          status: ''
        }
      }
    },
    mounted () {
      this.handleSearch()
    },
    methods: {
      handleSearch (model) {
        if (model !== undefined) {
          this.search.q = model
        }
        control.search(this.search).then(res => {
          this.table = res.data.data
          this.total = parseInt(res.data.total)
          oplog.create(`${this.name}`)
        })
      },
      handleCreate () {
        this.$refs.createRef.validate(valid => {
          if (valid) {
            control.create(this.createModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.createRef.resetFields()
                this.$message({type: 'success', message: this.name+'', duration: 1000})
                oplog.create(`${this.name}`)
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleUpdate () {
        this.$refs.updateRef.validate(valid => {
          if (valid) {
            control.update(this.updateId, this.updateModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.updateRef.resetFields()
                this.$message({type: 'success', message: this.name+'', duration: 1000})
                this.updateId = ''
                this.currentTab = '0'
                this.handleSearch()
                oplog.create(`${this.name}`)
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleAction (type, row) {
        if (type == 'delete') {
          control.delete(row.id).then(res => {
            if (res.data && res.data.status.code == '0') {
              // let index = this.table.indexOf(row)
              // this.table.splice(index, 1)
              this.handleSearch()
              oplog.create(`${this.name}`)
            }
          })
        } else {
          this.currentTab = '2'
          this.updateId = row.id
          for (var i in this.updateModel) {
            this.updateModel[i] = row[i]
          }
        }
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleSearch()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleSearch()
      }
    }
  }
</script>


