import Vue from 'vue'
import Vuex from 'vuex'
import { deepCopy } from '../api'
import current, { constantRouterMap, asynRouterMap } from '../router.js'

Vue.use(Vuex)

function filterAsynRouter (router, roles) {
  let n = []
  router.map(item => {
    roles.map(k => {
      if (item.uiTemplate == k.title) {
        var key = deepCopy(k)
        key.id = item.id 
        if (key.props) {
          key.props.routeId = item.id
        }  
        if (item.parentId == '0') {
          key.path = (item.routePrefix == '/' ? item.routePrefix : item.routePrefix + '/') + item.routeUrl
          n.splice(item.sequence, 0, key)
        } else {
          n.map(p => {
            if (p.props && p.props.routeId == item.parentId) { 
              key.path = (item.routePrefix == '/' ? '' : item.routePrefix + '/') + item.routeUrl
              if (p.children !== undefined) {
                if (item.sequence >= p.children.length) {
                  p.children.push(key)
                } else {
                  p.children.splice(item.sequence, 0, key)
                }
              } 
            }
          })
        }
      }
    })
  })
  return n
}

export default new Vuex.Store({
  state: {
    routers: constantRouterMap, // DEFAULT
    backRouters: [],
    addRouters: [], // ADD
    headers: [], // HEADER
    sidebar: [] // SIDEBAR
  },

  mutations: {
    SET_ROUTERS (state, routers) {
      state.addRouters = routers
      state.routers = constantRouterMap.concat(routers)
    },
    SET_HEADERS (state, data) {
      state.headers = []
      state.backRouters = data
      data.map(item => {
        if (item.parentId == 0) {
          let path = (item.routePrefix == '/' ? item.routePrefix : item.routePrefix + '/') + item.routeUrl
          let key = {label: item.name, path: path}
          if (item.sequence >= state.headers.length) {
            state.headers.push(key)
          } else {
            state.headers.splice(item.sequence, 0, key)
          }
        }
      })
    },
    SET_SIDEBAR (state, routeId) {
      let parentPath = state.backRouters.find(p => p.id == routeId).routeUrl
      state.sidebar = []
      state.backRouters.map(item => {
        if (item.parentId == routeId)  {
          let path = '/' + parentPath + (item.routePrefix == '/' ? item.routePrefix : item.routePrefix + '/') + item.routeUrl
          if (item.sequence == 0) {
            current.push(path)
          }
          if (item.sequence >= state.sidebar.length) {
            state.sidebar.push({name: item.name, path: path})
          } else {
            state.sidebar.splice(item.sequence, 0, {name: item.name, path: path})
          }
        }
      })
    }
  },

  actions: {
    handleRouters ({ commit }, data) {
      commit('SET_HEADERS', data)
      let n = filterAsynRouter(data, asynRouterMap)
      commit('SET_ROUTERS', n)
      current.addRoutes(n)
    },
    handleSidebar ({ commit }, routeId) {
      commit('SET_SIDEBAR', routeId)
    }
  }
})


