<template>
  <div class="body">
    <div class="container">
      <div class="system-content">
        <div class="face-compare">
          <div class="face-compare-left">
            <div class="border flex">
              <img :src="faceImg" v-if="faceImg">
              <i class="el-icon-plus" v-else></i>
            </div>
            <upload @change="handleUpload">
              <div class="upload-btn"><i class="el-icon-upload2" style="margin-right: 10px;"></i></div>
            </upload>
            <div style="margin-bottom: 10px;color: #9F9F9F;"></div>
            <div class="border">
              <el-checkbox-group v-model="compare.facesets">
                <el-checkbox v-for="item in table" :key="item.name" :label="item.id">{{item.name}}</el-checkbox>
              </el-checkbox-group>
            </div>
            <div class="upload-btn insert" @click="handleCompare"><i class="el-icon-search" style="margin-right: 10px;"></i></div>
          </div>
          <div class="face-compare-right">
            <div class="face-compare-right-item" v-for="(item,i) in result" :key="i">
              <div class="title">{{item.name}}</div>
              <div class="list">
                <div class="list-item" v-for="(key,index) in item.children" :key="index">
                  <div class="list-item-bg">
                    <div class="list-item-img"><img :src="key.url"></div>
                    <div class="list-item-text">
                      <div class="list-item-text__name"></div>
                      <div class="list-item-text__id">420821190010011110</div>
                      <div class="list-item-text__green">{{key.similarity}}%</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  
</template>

<script>
  import {faceset, face} from '../../api/index.js'
  import upload from '../../shared/components/upload.vue'
  export default {
    components: {
      upload
    },
    data () {
      return {
        faceImg: '',
        table: [],
        compare: {
          facesets: [],
          threshold: 30,
          image: {
            imageBase64: ''
          },
          pageNumber: 1,
          pageSize: 10
        },
        result: []
      }
    },
    mounted () {
      this.handleFaceset()
    },
    methods: {
      handleFaceset () {
        faceset.search('', 1, 10).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.table = res.data.data
            this.table.map(item => {
              this.compare.facesets.push(item.id)
            })
          }
        })
      },
      handleUpload (src) {
        this.faceImg = src
        this.compare.image.imageBase64 = src.split(',')[1]
      },
      handleCompare () {
        let obj = {name: '', children: []}
        face.retrieval(this.compare).then(res => {
          if (res.data && res.data.status.code == 0) {
            res.data.data.map(item => {
              this.table.map(key => {
                if (item.facesetId == key.id) {
                  obj.name = key.name
                  obj.children.push(item)
                }
              })
            })
            this.result.push(obj)
          } else {
            this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .face-compare
    height 100%
    background-color #fff 
    display flex
    justify-content space-between
    padding 12px
    &-left 
      width 150px
      margin-right 12px
      .border 
        border 1px solid #BFBFBF
        border-radius 4px
        color #BFBFBF
        font-size 30px
        min-height 150px
        width 150px
        position relative 
        overflow hidden
        &.flex 
          align-items center
          display flex 
          justify-content center
        > img 
          width 100%
        .el-checkbox-group
          display flex 
          flex-direction column
          .el-checkbox:first-child 
            margin-left 15px
      .upload-btn
        align-items center
        border 1px solid #00b99c
        border-radius 4px
        color #00b99c
        cursor pointer
        display flex 
        height 38px
        justify-content center
        margin 10px auto 30px
        &.insert
          background-color #00b99c
          color #fff
    &-right 
      flex 1
      &-item 
        border 1px solid #E0E3E4
        background-color #F4F8FA
        border-radius 4px
        padding 12px
        &:not(:first-child)
          margin-top 20px
        .title 
          color #8AA1BF
          font-size 14px
          padding-bottom 12px
          border-bottom 1px solid #F0F1F2
        .list 
          margin-left -10px 
          margin-right -10px 
          box-sizing border-box
          position relative
          &:before 
            display table 
            content ''
          &:after 
            display table 
            content ''
            clear both
          &-item 
            box-sizing border-box
            float left
            padding 0 10px
            width 25%
            margin-top 20px
            &-bg 
              background-color #fff 
              border 1px solid #DFE2E3
              border-radius 4px 
              overflow hidden
            &-img 
              background-color #DCD9E1
              height 170px
              display flex 
              align-items center 
              justify-content center
              > img 
                max-height 100%
                max-width 100%
            &-text 
              background-color #fff
              color #9F9F9F
              padding 0 10px
              line-height 30px
              &__green 
                color #00B799
                font-size 20px
</style>


