<template>
  <div class="body">
    <div class="container">
      <div class="system-content">
        <el-tabs v-model="currentTab">
          <el-tab-pane :label="name+''" name="0">
            <v-search :search-text="name+''" style="margin-bottom: 20px;" @search="handleSearch">
              <span slot="left">&nbsp;</span>
            </v-search>

            <v-table :data="table" :column="column" @row-click="rowClick" @update="handleAction"></v-table>
            <v-page :total="total" @size-change="sizeChange" @current-change="currentChange"></v-page>
          </el-tab-pane>
          <el-tab-pane :label="name+''" name="1">
            <el-form ref="createRef" label-width="60px" label-position="left" style="width: 50%;margin: 0 auto;" :model="createModel">
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="name">
                <el-input v-model.trim="createModel.name"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
                <el-input v-model.trim="createModel.description"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' class="btn" @click="handleCreate"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane :label="name+''" name="2" v-if="this.updateId">
            <el-form ref="updateRef" label-width="60px" label-position="left" style="width: 50%;margin: 0 auto;" :model="updateModel">
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="name">
                <el-input v-model.trim="updateModel.name"></el-input>
              </el-form-item>
              <el-form-item label="" :rules='[{required: true, message: ""}]' prop="description">
                <el-input v-model.trim="updateModel.description"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' class="btn" @click="handleUpdate"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
  import {group, oplog} from '../../api/index.js'
  export default {
    data () {
      return {
        name: '',
        column: group.column,
        table: [],
        total: 0,
        search: {
          q: '',
          pageSize: 10,
          pageNumber: 1
        },
        currentTab: '0',
        updateId: '',
        createModel: {
          name: '',
          description: ''
        },
        updateModel: {
          name: '',
          description: ''
        }
      }
    },
    mounted () {
      this.handleSearch()
    },
    methods: {
      rowClick (row) {
        this.$router.push({name: 'userGroupDetail', params: {id: row.id}})
      },
      handleCreate () {
        this.$refs.createRef.validate(valid => {
          group.create(this.createModel).then(res => {
            if (res.data && res.data.status.code == 0) {
              this.$refs.createRef.resetFields()
              this.handleSearch()
              this.$message({type: 'success', message: ''+this.name+'', duration: 1000})
              oplog.create(`${this.name}`)
            } else {
              this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
            }
          })
        })
      },
      handleUpdate () {
        this.$refs.updateRef.validate(valid => {
          if (valid) {
            group.put(this.updateId, this.updateModel).then(res => {
              if (res.data && res.data.status.code == 0) {
                this.$refs.updateRef.resetFields()
                this.$message({type: 'success', message: ''+this.name+'', duration: 1000})
                this.updateId = ''
                this.currentTab = '0'
                this.handleSearch()
                oplog.create(`${this.name}`)
              } else {
                this.$message({type: 'warning', message: res.data.status.message, duration: 1000})
              }
            })
          }
        })
      },
      handleSearch (model) {
        this.search.q = model == undefined ? '' : model
        group.search(this.search.q, this.search.pageNumber, this.search.pageSize).then(res => {
          if (res.data && res.data.status.code == 0) {
            this.table = res.data.data
            this.total = parseInt(res.data.total)
            oplog.create(`${this.name}`)
          }
        })
      },
      handleAction (type, row) {
        if (type == 'delete') {
          group.delete(row.id).then(res => {
            if (res.data && res.data.status.code == '0') {
              this.handleSearch()
              oplog.create(`${this.name}`)
            }
          })
        } else {
          this.currentTab = '2'
          this.updateId = row.id
          for (var i in this.updateModel) {
            this.updateModel[i] = row[i]
          }
        }
      },
      sizeChange (val) {
        this.search.pageSize = val 
        this.handleSearch()
      },
      currentChange (val) {
        this.search.pageNumber = val 
        this.handleSearch()
      }
    }
  }
</script>


