<template>
  <div class="table">
    <el-table :data="data" stripe @row-click="rowClick">
      <template v-for="(item, i) in column">
        <template v-if="item.type == 'slot'">
          <slot :name="item.prop"></slot>
        </template>
        <el-table-column v-else
          :key="i" :label="item.label" 
          :prop="item.prop" 
          :width="item.width" 
          :align="align"
        >
        </el-table-column>
      </template>

      <el-table-column label="" width="140px" :align="align" v-if="action.length > 0">
        <template scope="scope">
         <!--  <el-button @click.stop="handleAction(item, scope.row)" type="text" v-for="(item, i) in action" :key="i">
            <i :class="'el-icon-' + item"></i>
          </el-button> -->
          <el-button @click.stop="handleAction(item, scope.row)" class="btn-green" type="text" v-for="(item, i) in action" :key="i">
            {{item}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'v-table',
    props: {
      align: {
        type: String,
        default: 'center'
      },
      column: { // table
        type: Array,
        default: function () {
          return []
        }
      },
      action: { // 
        type: Array,
        default: function () {
          return ['', '']
        }
      },
      data: { // table
        type: Array,
        default: function () {
          return []
        }
      }
    },
    methods: {
      // 
      handleAction (type, row) {
        if (type == '') {
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$emit('update', 'delete', row)
          }).catch(() => {})
        } else {
          this.$emit('update', 'add', row)
        }
      },
      rowClick (row) {
        this.$emit('row-click', row)
      }
    }
  }
</script>

<style lang="stylus">
  .table 
    // padding 20px 10px
    background-color #edf2f5
    // padding-top 20px 
    &-img 
      align-items center
      display flex 
      margin 5px auto
      height 127px
      justify-content center
      background url('../../platform/shared/img/no_pic_bg.png') no-repeat center center
      width 103px
      > img 
        max-height 100%
        max-width 100%
    .el-table 
      border none
      &:after,&:before 
        background-color transparent
      th,td 
        border none
        height 50px
        &:not(:first-child)
          border-left 1px solid #F5F7F9
      th 
        color #666
        font-family MicrosoftYaHei Bold 
        font-size 14px 
      td 
        color #121212
        font-family MicrosoftYaHeiLight
        font-size 14px
        i 
          color #00B0C1
          font-size 16px
</style>


