<template>
  <div class="app">
    <v-header :nav="$store.state.headers"></v-header>
    <router-view></router-view>
    <v-footer></v-footer>
  </div>
</template>

<script>
  import {page} from '../shared/api/index.js'
  import footer from './shared/components/footer.vue'
  export default {
    components: {
      'v-footer': footer
    },
    mounted () {
      page.router().then(res => {
        if (res.data && res.data.status.code == 0) {
          this.$store.dispatch('handleRouters', res.data.data)
        }
      })
    }
  }
</script>

<style lang="stylus">
  @import './shared/stylus/reset.styl'
  @import './shared/stylus/config.styl'
  
  html,body
    background-color _body_bg
    height 100%
    font-family "Microsoft YaHei"
    .app
      // background url('./ui/home.png') no-repeat center top
      height 100%
      // min-height 1700px
      position relative
      padding-top (_header_height)
      padding-bottom _footer_height
      box-sizing border-box
      > .body 
        box-sizing border-box
        display flex
        min-height 100%
        padding-top 12px
        padding-bottom 12px
        .body 
          flex 1
  .btn-green 
    color _btn_bg
    &:hover,&:active,&:visited
      color lighten(_btn_bg, 5)
  .has-footer
    padding-bottom (_footer_height + 20px)
  .is-bottom 
    bottom 0
    left 0
    position absolute
    width 100%
  .container 
    margin 0 auto
    width _container
    position relative
    .container 
      flex 1
      width auto
  .bg-white 
    background-color #fff
  .margin-top 
    margin-top 20px
  .padding 
    padding 10px
  .text-center 
    text-align center
  .system 
    display flex
    min-height 100%
    &-header 
      padding 10px 0 20px
      .btn-drop 
        background-color _btn_bg
        border-radius 20px
        color #fff
        font-size 14px
        display inline-block
        margin-right 20px
        padding 11px 15px
    &-content 
      flex 1
      display flex 
      flex-direction column
      .flex 
        display flex 
        flex 1
        margin-bottom 12px
        &-item 
          background-color #fff
          // background-color rgba(0,0,0, .5)
          position relative
          width 100%
          &:not(:first-child)
            margin-left 12px
          &.flex-fix 
            flex 1
          .title 
            background-color #f5fafc
            color #0a0b0e
            font-family MicrosoftYaHei Bold 
            font-size 14px
            line-height 50px 
            padding-left 12px
      .el-tabs 
        background-color #fff
        height 100%
        display flex 
        flex-direction column
      .el-tabs__header 
        background-color #edf2f5
        border-bottom none
        margin-bottom 0
      .el-tabs__content 
        flex 1
      .is-active 
        background-color #fff
      .el-tabs__active-bar 
        bottom auto 
        top 0
      .el-tab-pane 
        background-color #fff
        box-sizing border-box
        height 100%
        padding-top 20px
        .table 
          padding-top 20px
</style>


