<template>
  <div class="search-tree">
    <div class="tree-tag" v-show="tags.length > 0">
      <span class="title"> ></span><el-tag v-for="(item, i) in tags" :key="i" :closable="true" @close="handleTagClose($event, i)">{{item.name}}</el-tag>
    </div>
    <div class="search-bar">
      <el-input placeholder="" v-model="inputModel">
          <!-- <a class="btn pull-right" slot="append" @click="handleBtnSearch"></a> -->
          <el-button slot="append" icon="search" @click="handleBtnSearch"></el-button>
      </el-input>
    </div>
    <el-collapse-transition>
      <div style="position: relative;">
        <div class="tree" v-show="treeShow">
          <div class="tree-header"><i class="el-icon-plus pull-right"></i></div>
          <el-tree 
            :data="treeJson" 
            :props="props" 
            :show-checkbox="showCheckbox" 
            :highlight-current="!showCheckbox"
            :expand-on-click-node="showCheckbox"
            @node-click="handleNodeClick"
            @check-change="handleCheckChange">
          </el-tree>
        </div>
        <a class="btn tree-save" @click="handleTreeShow" v-show="treeShow"></a>
      </div>
    </el-collapse-transition>
    <slot name="footer" v-if="$slots.footer"></slot>
  </div>
</template>

<script>
  export default {
    name: 'search-tree',
    props: {
      showCheckbox: {
        type: Boolean,
        default: true
      },
      treeData: {
        type: Array,
        default: function () {
          return []
        }
      },
    },
    data () {
      return {
        props: {
          label: 'name',
          children: 'children'
        },
        inputModel: '',
        treeJson: [],
        checked: [],
        tags: [],
        treeShow: false
      }
    },
    watch: {
      treeData (val) {
        if (val) {
          this.treeJson = this.jsonTree(val, 'id', 'parentId', 'children')
          this.treeShow = true
        }
      }
    },
    methods: {
      handleBtnSearch () {
        this.$emit('search', this.inputModel)
        this.checked = []
      },
      handleTreeShow () {
        this.treeShow = false
        this.tags = []
        this.tags = this.tags.concat(this.checked)
        this.$emit('update', this.tags)
      },
      handleTagClose (e, i) {
        this.tags.splice(i, 1)
        this.$emit('update', this.tags)
      },
      handleNodeClick (a, b, c) {
        if (!this.showCheckbox) {
          this.checked.push(a)
        }
      },
      handleCheckChange (data, checked, indeterminate) {
        // console.log(data)
        if (checked) {
          this.checked.push(data)
        } else {
          let i = this.checked.indexOf(data)
          this.checked.splice(i, 1)
        }
      },
      jsonTree (json, id, pid, children) {
        var r = [], hash = {}, i = 0, j = 0, len = json.length
        for (;i < len; i++) {
          hash[json[i][id]] = json[i]
        }
        for (;j < len; j++) {
          var val = json[j], hval = hash[val[pid]]
          if (hval) {
            !hval[children]&&(hval[children] = [])
            hval[children].push(val)
          } else {
            r.push(val)
          }
        }
        return r
      }
    }
  }
</script>

<style lang="stylus">
  .search-tree 
    background-color #fff 
    padding 12px 20px 
    .search-bar 
      width 305px
      display flex
      justify-content space-between
      height 36px
    .el-input 
      width 240px
    input 
      background-color #f1f6f9
      height 36px
      color #999
      font-family MicrosoftYaHeiLight
      font-size 12px
      outline none
      &::-webkit-input-placeholder
        color #999
        font-family MicrosoftYaHeiLight
        font-size 12px
    .el-input-group__append
      background-color #EAEFF2
      font-size 16px
    .btn-text 
      cursor pointer
      height 36px
      font-size 14px
      display inline-block
      box-sizing border-box
      color #00B99C
      padding 11px 15px
      margin-right 20px
      &.active 
        border-radius 20px
        background-color #00B99C
        color #fff
    .btn
      background-color #00B99C
      border-radius 4px
      box-sizing border-box
      border none
      color #fff
      cursor pointer
      display inline-block
      font-family MicrosfotYaHei 
      font-size 14px !important
      padding 11px 15px
      height 36px
      &:hover 
       background-color lighten(#00B99C, 5)
      &.tree-save 
        position absolute 
        left 400px
        bottom 0
    .tree 
      border 1px solid #bfcbd9
      border-radius 4px
      display inline-block
      margin-top 10px
      height  300px 
      overflow hidden
      width 302px
      &-tag 
        border-bottom 1px solid #ddd
        margin-bottom 12px
        padding-bottom 12px
        .title 
          color #666
          font-size 14px 
        .el-tag 
          background-color #fff
          border 1px solid #999
          color #333
          margin-left 20px
      &-header 
        border-bottom 1px dashed #ddd
        line-height 36px
        padding-left 10px
        color #999
        font-size 14px
        .pull-right 
          background-color #E7F0F0
          cursor pointer
          color #00BCA1
          line-height 36px
          text-align center
          width 40px
      &-footer 
        margin-top 12px
      .el-tree 
        border none
</style>


