<template>
  <div class="place" style="height: 60px;" v-show="total > 10">
    <div class="pagination" :style="{'justify-content': aligned}">
      <!-- <div class="total">{{total}},</div>
      <el-select v-model="size" @change="sizeChange">
        <el-option
        v-for="item in pageSizes"
          :key="item"
          :label="item"
          :value="item">
          <span>{{item}} </span>
        </el-option>
      </el-select> -->
      <el-pagination small 
        :total="total" 
        :page-size="pageSize"
        :page-sizes="pageSizes"
        :current-page.sync="currentPage"
        layout="total, sizes, prev, pager, next, jumper" 
        @current-change="handleCurrent"
        @size-change="handleSize">  
      </el-pagination>
      <!-- <div class="jump"><el-input v-model="inputPage" @keyup.enter.native="currentChange"></el-input></div> -->
    </div>
  </div>
</template>

<script>
  export default {
    name: 'v-page',
    props: {
      align: {
        type: String,
        default: 'center'
      },
      total: {
        type: Number,
        default: 0
      },
      pageSizes: {
        type: Array,
        default: function () {
          return [10, 20, 30, 40]
        }
      },
      current: {
        type: Number,
        default: 1
      }
    },
    computed: {
      aligned () {
        if (this.align == 'left') {
          return 'flex-start'
        } else if (this.align == 'center') {
          return 'center'
        } else {
          return 'flex-end'
        }
      }
    },
    data () {
      return {
        size: 10,
        pageSize: 10,
        currentPage: this.current,
        inputPage: this.current
      }
    },
    methods: {
      handleSize (size) {
        this.$emit('size-change', size)
      },
      handleCurrent (size) {
        this.inputPage = size
        this.$emit('current-change', size)
      },
      sizeChange (val) {
        this.pageSize = val
        this.$emit('size-change', val)
      },
      currentChange () {
        this.currentPage = parseInt(this.inputPage)
        this.$emit('current-change', this.currentPage)
      }
    }
  }
</script>

<style lang="stylus">
  _border_color = #f5f5f5
  _bg_color = #f9f9f9
  _height = 36px

  .pagination
    align-items center
    box-sizing border-box
    color #bfbfbf
    display flex
    font-size 14px
    padding 10px 20px
    width 100%
    left 0
    bottom 0
    position absolute
    .el-pagination 
      .btn-prev,.btn-next
        color #bfbfbf
        height 28px
        line-height 28px
        width _height
      .el-pager 
        .number
          background-color transparent
          color #bfbfbf
          height 28px
          line-height 28px
          font-size 16px
          width _height
          &.active 
            background-color transparent
            border none
            color #93F5CB
      
</style>


