import fetch from './fetch.js'
import cookie from 'js-cookie'
import jwt from 'jwt-simple'

class Fetch {
  constructor (url) {
    this.url = url
    this.init = this.init.bind(this)
    this.get = this.get.bind(this)
    this.create = this.create.bind(this)
    this.search = this.search.bind(this)
    this.put = this.put.bind(this)
    this.delete = this.delete.bind(this)
  }
  init (page) {
    var url = page == undefined ? this.url + '?q=%25%25&pageSize=10&pageNumber=1' : this.url + '?pageToken=' + page
    return fetch.get(url)
  }
  get (id) {
    return fetch.get(this.url+'/'+id)
  }
  create (params) {
    return fetch.post(this.url, params)
  }
  search (q, number = 1, size = 10) {
    let url = this.url + '?q=' + encodeURI('%' + q + '%') + '&pageSize=' + size + '&pageNumber=' + number
    return fetch.get(url)
  }
  put (id, params) {
    return fetch.put(this.url+'/'+id, params)
  }
  delete (id) {
    return fetch.delete(this.url+'/'+id)
  }
}
