class omFetch extends Fetch {
  constructor () {
    super('omunits')
    this.column = [
      {label: '', prop: 'name'},
      {label: '', prop: 'description'},
      {label: '', prop: 'parentId'},
      {label: '', prop: 'categoryId'},
      {label: '', prop: 'created'},
      {label: '', prop: 'creator'}
    ]
    this.update = {
      inputs: [
        {prop: 'name', label: ''},
        {prop: 'description', label: ''},
        {prop: 'parentId', label: ''},
        {prop: 'categoryId', label: ''}
      ]
    }
  }

  users (id) {
    let url = id + '&q=%25%25&pageSize=10&pageNumber=1'
    return fetch.get('users?omUnitId=' + url) 
  }
  addUser (id, uids) {
    // "uids": [907093274202734592, 907172319573573632]
    return fetch.post(this.url+'/'+id+'/staffs', uids)
  }
  delUser (id, uids) {
    let url = ''
    uids.forEach((item, i) => {
      if (i > 0) {
        url += ('&uids=' + item)
      } else {
        url = item
      }
    })
    return fetch.delete(this.url+'/'+id+'/staffs?uids=' + url)
  }
}
